import { Dispatch } from '../base';
import { Texture } from '../textures';

export default class Assets extends Dispatch {
    constructor() {
        super();
    }

    init() {
        super.init();
    }

    destroy() {
        super.destroy();
    }

    start() {
        super.start();
    }

    stop() {
        super.stop();
    }

    getTexture(imageUrl, crossorigin) {
        return Texture.fromImage(imageUrl, crossorigin);
    }
}



