
var _canvas = null;
var _dpi = 0;

export default class Browser {
    constructor() {
    }

    static get canvas() {
        return _canvas || (_canvas = Browser.createElement('canvas'));
    }

    static addChild(child) {
        document.body.appendChild(child.element);
    }

    static get context() {
        var context = Browser.canvas.getContext('2d');
        // context.clearRect(0, 0, canvas.width, canvas.height);
        return context;
    }

    static dataURLtoBlob(dataURL) {
        // convert base64 to raw binary data held in a string
        // doesn't handle URLEncoded DataURIs - see SO answer #6850276 for code that does this
        var byteString = atob(dataURL.split(',')[1]);

        // separate out the mime component
        var mimeString = dataURL.split(',')[0].split(':')[1].split(';')[0];

        // write the bytes of the string to an ArrayBuffer
        var ab = new ArrayBuffer(byteString.length);

        // create a view into the buffer
        var ia = new Uint8Array(ab);

        // set the bytes of the buffer to the correct values
        for (var i = 0; i < byteString.length; i++) {
            ia[i] = byteString.charCodeAt(i);
        }

        // write the ArrayBuffer to a blob, and you're done
        var blob = new Blob([ab], {type: mimeString});

        return blob;
    }

    static toDataURL(source, x = 0, y = 0, w, h, type='image/png') {
        Browser.canvas.width = w;
        Browser.canvas.height = h;
        Browser.context.drawImage(source, x, y, w, h, 0, 0, w, h);
        return Browser.canvas.toDataURL(type);
    }

    static drawGrid(context, source, rect, left, top, right, bottom, width, height) {
        if (!context) return;
        const x = rect.left;
        const y = rect.top;
        const w = rect.width;
        const h = rect.height;

        left = left || 0;
        top = top || 0;
        right = right || 0;
        bottom = bottom || 0;

        left && top && context.drawImage(source, x, y, left, top, 0, 0, left, top);
        top && context.drawImage(source, x + left, y, w - left - right, top, left, 0, width - left - right, top);
        right && top && context.drawImage(source, x + w - right, y, right, top, width - right, 0, right, top);
        left && context.drawImage(source, x, y + top, left, h - bottom - top, 0, top, left, height - bottom - top);
        context.drawImage(source, x + left, y + top, w - right - left, h - bottom - top, left, top, width - right - left, height - bottom - top);
        right && context.drawImage(source, x + w - right, y + top, right, h - bottom - top, width - right, top, right, height - bottom - top);
        left && bottom && context.drawImage(source, x, y + h - bottom, left, bottom, 0, height - bottom, left, bottom);
        bottom && context.drawImage(source, x + left, y + h - bottom, w - left - right, bottom, left, height - bottom, width - right - left, bottom);
        right && bottom && context.drawImage(source, x + w - right, y + h - bottom, right, bottom, width - right, height - bottom, right, bottom);
    }

    static fromGrid(source, rect, left, top, right, bottom, width, height, type='image/png') {
        Browser.canvas.width = width;
        Browser.canvas.height = height;
        Browser.drawGrid(Browser.context, source, rect, left, top, right, bottom, width, height);

        return Browser.canvas.toDataURL(type);
    }

    static createElement(element) {
        return document.createElement(element);
    }

    static get dpi() {
        if (_dpi == 0) {
            var arrDPI = new Array;
            if (window.screen.deviceXDPI) {
                arrDPI[0] = window.screen.deviceXDPI;
                arrDPI[1] = window.screen.deviceYDPI;
            } else {
                var tmpNode = document.createElement('div');
                tmpNode.style.cssText = 'width:1in;height:1in;position:absolute;left:0px;top:0px;z-index:99;visibility:hidden';
                document.body.appendChild(tmpNode);
                arrDPI[0] = parseInt(tmpNode.offsetWidth);
                arrDPI[1] = parseInt(tmpNode.offsetHeight);
                tmpNode.parentNode.removeChild(tmpNode);
            }
            _dpi = arrDPI[0];
        }

        return _dpi;
    }
}



