<template>
    <div :class="classes" ref="row" :style="sizeStyle">
        <template v-if="renderType === 'html'"><span v-html="data" :style="sizeStyle"></span></template>
        <template v-if="renderType === 'normal'">
            <span :style="sizeStyle">{{data}}</span>
        </template>
        <list-expand
            v-if="renderType === 'render'"
            :item="item"
            :index="index"
            :width="width"
            :height="height"
            :hovered="hovered"
            :selected="selected"
            :render="option.render"></list-expand>
        <list-slot
            v-if="renderType === 'slot'"
            :item="item"
            :slot="option.slot"
            :width="width"
            :height="height"
            :hovered="hovered"
            :selected="selected"
            :index="index"></list-slot>
    </div>
</template>
<script>
    import ListExpand from './expand';
    import ListSlot from './slot';

    export default {
        name: 'ListItem',
        components: { ListExpand, ListSlot },
        props: {
            prefixCls: String,
            item: [Object, String],
            option: Object,
            width: Number,
            height: Number,
            selected: Boolean,
            hovered: Boolean,
            index: Number,           // _index of data
        },
        data () {
            return {
                renderType: 'normal',
            };
        },
        computed: {
            classes () {
                return [
                    `${this.prefixCls}-item`,
                ];
            },
            sizeStyle () {
                return {
                    width: (this.width && `${this.width}px`) || 'auto',
                    height: (this.height && `${this.height}px`) || 'auto',
                };
            }
        },
        methods: {
        },
        created () {
            if (!this.option) {
                this.renderType = 'normal';
            } else if (this.option.type === 'html') {
                this.renderType = 'html';
            } else if (this.option.render) {
                this.renderType = 'render';
            } else if (this.option.slot) {
                this.renderType = 'slot';
            } else {
                this.renderType = 'normal';
            }
        },
    };
</script>



