<template>
    <Bitmap
        :class="classes"
        @on-click="handleClicked"
        @on-mouseenter="handleMouseenter"
        @on-mouseover="handleMouseOver"
        @on-mouseleave="handleMouseLeave"
        @on-mousedown="handleMouseDown"
        @on-mouseup="handleMouseUp"
        @on-resize="handleResize"
        v-bind="tagProps">
        <slot></slot>
    </Bitmap>
</template>
<script>
    import Bitmap from '../bitmap';

    import mixinsSkin from '../../mixins/skin';
    import mixinsTexture from '../../mixins/texture';

    const prefixCls = 'yui-clip';

    export default {
        name: 'clip',
        mixins: [ mixinsSkin, mixinsTexture ],
        components: { Bitmap },
        props: {
            frame: {
                type: Number,
                default: 0
            },
            clips: {
                type: [Array, String, Object],
                default () {
                    return [1, 1, 0];
                }
            },
            grids: {
                type: [Array, String, Object],
                default () {
                    return [];
                }
            },
            autosize: {
                type: String
            },
            slotClass: {
                type: String
            },
        },
        data() {
            return {
                gridSource: [],
                textures: [],
                clipSource: [],
                colors: [],
                backColors: [],
            };
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`
                ];
            },
            tagProps() {
                // const style = this.ui_size(this.width, this.height);
                const mouseEnable = this.mouseEnable;
                const skin = this.url;
                const width = this.width;
                const height = this.height;
                const grids = this.imgGrid;
                const autosize = this.autosize;
                const slotClass = this.slotClass;
                const color = this.showColor;
                const backgroundColor = this.showBackColor;
                const textAlign = this.textAlign;
                return { mouseEnable, skin, width, height, grids, autosize, slotClass, color, backgroundColor, textAlign };
            },
            current() {
                var result = null;
                if (this.textures.length > 0) {
                    var index = this.frame || 0;
                    if (index >= this.textures.length) {
                        index = (index < 3) ? (this.textures.length - 1) : 0;
                    }
                    result = this.textures[index];
                }
                return result;
            },
            imgGrid() {
                var result = [];
                if (this.gridSource.length > 0) {
                    var index = this.frame || 0;
                    if (index >= this.gridSource.length) {
                        index = (index < 3) ? (this.gridSource.length - 1) : 0;
                    }
                    result = this.gridSource[index];
                }
                return result;
            },
            showColor() {
                var result = null;
                if (this.colors && this.colors.length > 0) {
                    switch (this.frame) {
                        case 1: result = ((this.colors.length > 1) && this.colors[1]) || this.colors[0]; break;
                        case 2: result = ((this.colors.length > 2) && this.colors[2]) || ((this.colors.length > 1) && this.colors[1]) || this.colors[0]; break;
                        default: result = this.colors[0]; break;
                    }
                }
                return result;
            },
            showBackColor() {
                var result = null;
                if (this.backColors && this.backColors.length > 0) {
                    switch (this.frame) {
                        case 1: result = ((this.backColors.length > 1) && this.backColors[1]) || this.backColors[0]; break;
                        case 2: result = ((this.backColors.length > 2) && this.backColors[2]) || ((this.backColors.length > 1) && this.backColors[1]) || this.backColors[0]; break;
                        default: result = this.backColors[0]; break;
                    }
                }
                return result;
            },
        },
        methods: {
            start() {
                this.clipSource = this.clipsVal(this.clips);
                this.gridSource = this.makeGridsSource();
                this.colors = this.applyColors(this.color);
                this.backColors = this.applyColors(this.backgroundColor);
            },
            readyClips(source) {
                this.loadClips(source, this.clipSource, (textures) => {
                    this.textures = textures;
                    this.reload();
                });
            },
            reload() {
                this.url = this.current;
            },
            changed() {
                if (this.skin instanceof Array) {
                    this.textures = this.skin;
                    this.reload(this.skin);
                } else {
                    this.skin && ((this.clipSource.length < 1) ? this.reload([this.skin]) : this.readyClips(this.skin));
                }
            },
            makeGridsSource() {
                var result = [];
                var value = this.grids || [];
                if (value instanceof Array) {
                    result = value;
                } else if (typeof value === 'string') {
                    result = value.split('|') || [];
                } else {
                    result = [value];
                }
                return result;
            },
            handleClicked (event) {
                this.$emit('on-click', event);
            },
            handleMouseenter(event) {
                this.$emit('on-mouseenter', event);
            },
            handleMouseOver(event) {
                this.$emit('on-mouseover', event);
            },
            handleMouseLeave(event) {
                this.$emit('on-mouseleave', event);
            },
            handleMouseDown(event) {
                this.$emit('on-mousedown', event);
            },
            handleMouseUp(event) {
                this.$emit('on-mouseup', event);
            },
            handleResize(event) {
                this.$emit('on-resize', event);
            },
            applyColors(color) {
                var result = [];
                var value = color || [];
                if (value instanceof Array) {
                    result = value;
                } else if (typeof value === 'string') {
                    result = value.split('|') || [];
                } else {
                    result = [value];
                }
                return result;
            }
        },
        watch: {
            frame() {
                this.reload();
            },
            clips() {
                this.clipSource = this.clipsVal(this.clips);
                this.changed();
            },
            grids() {
                this.gridSource = this.makeGridsSource();
                this.changed();
            },
            color() {
                this.colors = this.applyColors(this.color);
            },
            backgroundColor() {
                this.backColors = this.applyColors(this.backgroundColor);
            },
        }
    };
</script>



