export default {
    name: 'ListSlot',
    functional: true,
    inject: ['listRoot'],
    props: {
        slot: String,
        width: Number,
        height: Number,
        item: [Object, String],
        index: Number,
        selected: Boolean,
        hovered: Boolean,
    },
    render: (h, ctx) => {
        return h('div', ctx.injections.listRoot.$scopedSlots[ctx.props.slot]({
            item: ctx.props.item,
            width: ctx.props.width,
            height: ctx.props.height,
            index: ctx.props.index,
            selected: ctx.props.selected,
            hovered: ctx.props.hovered,
        }));
    }
};



