<template>
    <component
        tabindex="0"
        :is="tagName"
        :class="wrapClasses"
        @click="toggle"
        @on-click="toggle"
        @keydown.space="toggle"
        @on-mouseover="handleMouseOver"
        @on-mouseleave="handleMouseLeave"
        @on-mousedown="handleMouseDown"
        @on-mouseup="handleMouseUp"
        v-bind="tagProps"
    >
        <input type="hidden" :name="name" :value="currentValue">
        <span :class="innerClasses">
            <slot name="open" v-if="currentValue === trueValue"></slot>
            <slot name="close" v-if="currentValue === falseValue"></slot>
        </span>
    </component>
</template>
<script>
    import Clip from '../clip';
    import { oneOf } from '../../utils/assist';
    import Emitter from '../../mixins/emitter';
    import mixinsSkin from '../../mixins/skin';

    const prefixCls = 'yui-switch';

    export default {
        name: 'USwitch',
        mixins: [ Emitter, mixinsSkin ],
        props: {
            value: {
                type: [String, Number, Boolean],
                default: false
            },
            trueValue: {
                type: [String, Number, Boolean],
                default: true
            },
            falseValue: {
                type: [String, Number, Boolean],
                default: false
            },
            disabled: {
                type: Boolean,
                default: false
            },
            size: {
                validator (value) {
                    return oneOf(value, ['large', 'small', 'default']);
                },
                default () {
                    return !this.$YUI || this.$YUI.size === '' ? 'default' : this.$YUI.size;
                }
            },
            name: {
                type: String
            },
            loading: {
                type: Boolean,
                default: false
            },
            trueRule: {
                type: [Array, String, Object]
            },
            falseRule: {
                type: [Array, String, Object]
            },
            slotClass: {
                type: String
            },
        },
        data () {
            return {
                currentValue: this.value,
                frame: 0,
                skinArray: [],
                trueSkinRule: {},
                falseSkinRule: {},
            };
        },
        computed: {
            tagName() {
                const {skin} = this;
                return (skin && Clip) || 'span';
            },
            wrapClasses () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-checked`]: (this.currentValue === this.trueValue) && !this.skin,
                        [`${prefixCls}-disabled`]: this.disabled,
                        [`${prefixCls}-${this.size}`]: !!this.size,
                        [`${prefixCls}-loading`]: this.loading,
                        [`${prefixCls}-default`]: !this.skin,
                        [`${prefixCls}-define`]: !!this.skin,
                    }
                ];
            },
            innerClasses () {
                return `${prefixCls}-inner`;
            },
            tagProps() {
                const style = { };
                this.width && (style.width = this.width);
                this.height && (style.height = this.height);
                const props = { style };

                props.mouseEnable = 'true';
                props.frame = this.frame;
                this.url && (props.skin = this.url);
                if (this.currentRule) {
                    this.currentRule.clips && (props.clips = this.currentRule.clips);
                    this.currentRule.grids && (props.grids = this.currentRule.grids);
                }
                this.width && (props.width = this.width);
                this.height && (props.height = this.height);
                this.slotClass && (props.slotClass = this.slotClass);
                this.color && (props.color = this.color);
                this.backgroundColor && (props.backgroundColor = this.backgroundColor);
                this.textAlign && (props.textAlign = this.textAlign);

                return props;
            },
            checked() {
                return (this.currentValue === this.trueValue);
            },
            currentSkin() {
                if (!this.skinArray || (this.skinArray.length < 1)) {
                    return null;
                }
                var index = (this.checked && 1) || 0;
                if (this.skinArray.length < index) {
                    index = 0;
                }
                return this.skinArray[index];
            },
            currentRule() {
                return (this.checked && this.trueSkinRule) || this.falseSkinRule;
            }
        },
        methods: {
            toggle (event) {
                event.preventDefault();
                if (this.disabled || this.loading) {
                    return false;
                }

                const checked = this.checked ? this.falseValue : this.trueValue;

                this.currentValue = checked;
                this.$emit('input', checked);
                this.$emit('on-change', checked);
                this.dispatch('FormItem', 'on-form-change', checked);
            },
            handleMouseOver() {
                this.frame = 1;
            },
            handleMouseLeave() {
                this.frame = 0;
            },
            handleMouseDown() {
                this.frame = 2;
            },
            handleMouseUp() {
                this.frame = 1;
            },
            start() {
                this.skinArray = (!(this.skin instanceof Array) && [this.skin]) || this.skin;
                this.trueSkinRule = this.reloadSource(this.trueRule);
                this.falseSkinRule = this.reloadSource(this.falseRule);
            },
            changed() {
                var skin = this.currentSkin;
                this.url = ((skin !== 'none') && skin) || '';
            },
            reloadSource(rule) {
                var result = {};
                if (rule) {
                    var value = rule;
                    if (typeof value === 'string') {
                        value = value.split('||') || [];
                    }
                    if (value instanceof Array) {
                        (value.length > 0) && (result.clips = value[0]);
                        (value.length > 1) && (result.grids = value[1]);
                    } else {
                        const {clips, grids} = value;
                        result = { clips, grids };
                    }
                }

                return result;
            }
        },
        watch: {
            value (val) {
                if (val !== this.trueValue && val !== this.falseValue) {
                    throw 'Value should be trueValue or falseValue.';
                }
                this.currentValue = val;
            },
            trueRule() {
                this.trueSkinRule = this.reloadSource(this.trueRule);
                this.changed();
            },
            falseRule() {
                this.falseSkinRule = this.reloadSource(this.falseRule);
                this.changed();
            },
            currentValue() {
                this.changed();
            }
        }
    };
</script>



