<template>
    <component
        :is="tagName"
        :disabled="disabled"
        :class="classes"
        @on-click="handleClicked"
        @on-mouseover="handleMouseOver"
        @on-mouseleave="handleMouseLeave"
        @on-mousedown="handleMouseDown"
        @on-mouseup="handleMouseUp"
        @click="handleClicked"
        v-bind="tagProps">
        <Icon class="yui-load-loop" type="ios-loading" v-if="loading"></Icon>
        <Icon :type="icon" :skin="!disabled && iconUrl || null" :style="iconStyles" :custom="customIcon" v-if="(icon || iconUrl || customIcon) && !loading"></Icon>
        <span v-if="showSlot" ref="slot"><slot></slot></span>
    </component>
</template>
<script>
    import Icon from '../icon';
    import Clip from '../clip';
    import { oneOf } from '../../utils/assist';
    import mixinsLink from '../../mixins/link';
    import mixinsSkin from '../../mixins/skin';

    const prefixCls = 'yui-btn';

    export default {
        name: 'Button',
        mixins: [ mixinsLink, mixinsSkin, ],
        components: { Icon, Clip },
        props: {
            type: {
                validator (value) {
                    return oneOf(value, ['default', 'primary', 'dashed', 'text', 'info', 'success', 'warning', 'error', 'define']);
                },
                default: 'default'
            },
            shape: {
                validator (value) {
                    return oneOf(value, ['circle', 'circle-outline']);
                }
            },
            size: {
                validator (value) {
                    return oneOf(value, ['small', 'large', 'default']);
                },
                default () {
                    return !this.$YUI || this.$YUI.size === '' ? 'default' : this.$YUI.size;
                }
            },
            loading: Boolean,
            disabled: Boolean,
            htmlType: {
                default: 'button',
                validator (value) {
                    return oneOf(value, ['button', 'submit', 'reset']);
                }
            },
            icon: {
                type: String,
                default: ''
            },
            iconSpace: {
                type: String,
                default: ''
            },
            iconUrl: {
                type: String,
                default: null
            },
            customIcon: {
                type: String,
                default: ''
            },
            long: {
                type: Boolean,
                default: false
            },
            ghost: {
                type: Boolean,
                default: false
            },
            rule: {
                type: [Array, String, Object]
            },
            slotClass: {
                type: String
            },
            selected: {
                type: Boolean,
                default: false
            },
        },
        data () {
            return {
                showSlot: true,
                frame: 0,
                ruleInfo: {}
            };
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    `${prefixCls}-${this.type}`,
                    {
                        [`${prefixCls}-long`]: this.long,
                        [`${prefixCls}-${this.shape}`]: !!this.shape,
                        [`${prefixCls}-${this.size}`]: this.size !== 'default',
                        [`${prefixCls}-loading`]: this.loading != null && this.loading,
                        [`${prefixCls}-icon-only`]: !this.showSlot && (!!this.icon || !!this.customIcon || this.loading),
                        [`${prefixCls}-ghost`]: this.ghost,
                        [`${prefixCls}-define`]: !!this.skin,
                    }
                ];
            },
            // Point out if it should render as <a> tag
            isHrefPattern() {
                const {to} = this;
                return !!to;
            },
            tagName() {
                const {isHrefPattern, skin} = this;
                return (skin && Clip) || (isHrefPattern ? 'a' : 'button');
            },
            iconStyles() {
                const style = {};

                if (this.iconSpace) {
                    style.marginRight = this.iconSpace;
                }

                return style;
            },
            tagProps() {
                const {isHrefPattern} = this;

                const style = { };
                style.width = this.width !== undefined && this.width || 'auto';
                style.height = this.height !== undefined && this.height || 'auto';
                const props = { style };

                props.mouseEnable = 'true';
                props.frame = this.frame;
                this.url && (props.skin = this.url);
                if (this.ruleInfo) {
                    this.ruleInfo.clips && (props.clips = this.ruleInfo.clips);
                    this.ruleInfo.grids && (props.grids = this.ruleInfo.grids);
                }
                this.width && (props.width = this.width);
                this.height && (props.height = this.height);
                this.slotClass && (props.slotClass = this.slotClass);
                this.color && (props.color = this.color);
                this.backgroundColor && (props.backgroundColor = this.backgroundColor);
                this.textAlign && (props.textAlign = this.textAlign);

                if(isHrefPattern) {
                    const {linkUrl,target}=this;
                    linkUrl && (props.linkUrl = linkUrl);
                    target && (props.target = target);
                } else {
                    const {htmlType} = this;
                    htmlType && (props.htmlType = htmlType);
                }
                return props;
            },
        },
        methods: {
            // Ctrl or CMD and click, open in new window when use `to`
            handleClicked (event) {
                if (this.disabled) {
                    return;
                }
                this.$emit('on-click', event);
                const openInNewWindow = event.ctrlKey || event.metaKey;
                this.handleCheckClick(event, openInNewWindow);
            },
            handleMouseOver() {
                if (this.disabled) {
                    return;
                }
                this.frame = 1;
            },
            handleMouseLeave() {
                if (this.disabled) {
                    return;
                }
                this.frame = (this.selected && 1) || 0;
            },
            handleMouseDown() {
                if (this.disabled) {
                    return;
                }
                this.frame = 2;
            },
            handleMouseUp() {
                if (this.disabled) {
                    return;
                }
                this.frame = 1;
            },
            start() {
                this.frame = (this.disabled && 3) || (this.selected && 1) || 0;
                this.showSlot = this.$slots.default !== undefined;
                this.ruleInfo = this.reloadSource();
            },
            changed() {
                this.url = ((this.skin !== 'none') && this.skin) || '';
            },
            reloadSource() {
                var result = {};
                var value = this.rule || {};
                if (typeof value === 'string') {
                    value = value.split('||') || [];
                }
                if (value instanceof Array) {
                    (value.length > 0) && (result.clips = value[0]);
                    (value.length > 1) && (result.grids = value[1]);
                } else {
                    const {clips, grids} = value;
                    result = { clips, grids };
                }
                return result;
            }
        },
        watch: {
            rule() {
                this.ruleInfo = this.reloadSource();
                this.changed();
            },
            selected() {
                this.frame = (this.disabled && 3) || (this.selected && 1) || 0;
            },
            disabled() {
                this.frame = (this.disabled && 3) || (this.selected && 1) || 0;
            }
        }
    };
</script>



