<template>
    <Bitmap
        :class="classes"
        @on-click="handleClicked"
        @on-mouseover="handleMouseOver"
        @on-mouseleave="handleMouseLeave"
        @on-mousedown="handleMouseDown"
        @on-mouseup="handleMouseUp"
        @on-resize="handleResize"
        v-bind="tagProps">
        <list-body
            :prefix-cls="prefixCls"
            :option="option"
            :data="data"
            :size="itemSize"
            :direction="direction"
            ref="body"></list-body>
    </Bitmap>
</template>
<script>
    import Bitmap from '../bitmap';
    import ListBody from './list-body.vue';
    import mixinsSkin from '../../mixins/skin';
    import mixinsTexture from '../../mixins/texture';
    import { oneOf } from '../../utils/assist';

    const prefixCls = 'yui-list';

    export default {
        name: 'list',
        mixins: [ mixinsSkin, mixinsTexture ],
        components: { Bitmap, ListBody },
        provide () {
            return {
                listRoot: this
            };
        },
        props: {
            rule: {
                type: [Array, String, Object]
            },
            data: {
                type: Array,
                default: ()=>[]
            },
            itemSize: {
                type: [Object, Array, String],
            },
            option: {
                type: Object,
                default: ()=>{}
            },
            direction: {
                validator (value) {
                    return oneOf(value, ['row', 'column']);
                },
                default: 'row'
            }
        },
        data() {
            return {
                innerW: 0,
                innerH: 0,
                prefixCls: prefixCls,
            };
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    // `${prefixCls}-${this.tagName}`
                ];
            },
            tagProps() {
                const style = { };
                style.width = this.width !== undefined && this.width || 'auto';
                style.height = this.height !== undefined && this.height || 'auto';
                const props = { style };

                props.mouseEnable = 'true';
                this.url && (props.skin = this.url);
                this.grids && (props.grids = this.grids);
                this.width && (props.width = this.width);
                this.height && (props.height = this.height);
                this.slotClass && (props.slotClass = this.slotClass);
                this.color && (props.color = this.color);
                this.backgroundColor && (props.backgroundColor = this.backgroundColor);
                this.textAlign && (props.textAlign = this.textAlign);

                this.autosize = 'true';

                return props;
            }
        },
        methods: {
            start() {
                this.innerW = parseFloat(this.width || 0);
                this.innerH = parseFloat(this.height || 0);
            },
            changed() {
                this.url = ((this.skin !== 'none') && this.skin) || '';
            },
            handleClicked (event) {
                this.$emit('on-click', event);
            },
            handleItemClicked (index) {
                this.$emit('on-item-click', index);
            },
            handleItemDbClicked (index) {
                this.$emit('on-item-dbclick', index);
            },
            handleItemSelected (index) {
                this.$emit('on-item-selected', index);
            },
            handleItemHovered (index) {
                this.$emit('on-item-hovered', index);
            },
            handleMouseenter(event) {
                this.$emit('on-mouseenter', event);
            },
            handleMouseOver(event) {
                this.$emit('on-mouseover', event);
            },
            handleMouseLeave(event) {
                this.$emit('on-mouseleave', event);
            },
            handleMouseDown(event) {
                this.$emit('on-mousedown', event);
            },
            handleMouseUp(event) {
                this.$emit('on-mouseup', event);
            },
            handleResize(width, height) {
                if (this.innerW !== width || this.innerH !== height) {
                    this.innerW = width;
                    this.innerH = height;
                    this.changed();
                    this.$emit('on-resize', {width, height});

                    this.$nextTick(() => {
                        this.$refs.body.resize(width, height);
                    });
                }
            },
        },
        watch: {
            width() {
                this.innerW = parseFloat(this.width || 0);
                this.changed();
            },
            height() {
                this.innerH = parseFloat(this.height || 0);
                this.changed();
            }
        }
    };
</script>



