<template>
    <div
        class="yui-bitmap"
        :style="styles"
        @click="handleClicked"
        @mouseenter="handleMouseenter"
        @mouseover="handleMouseOver"
        @mouseleave="handleMouseLeave"
        @mousedown="handleMouseDown"
        @mouseup="handleMouseUp"
        >
        <img v-if="url" :style="styles" class="yui-bitmap-back" :src="url">
        <div
            v-if="showSlot"
            :class="['yui-bitmap-face', slotClass]"
            :style="slotStyles"
            >
            <slot></slot>
        </div>
        <resize-observer v-if="canResize" @on-resize="handleResize" />
    </div>
</template>
<script>
    import ResizeObserver from '../base/resize';

    import mixinsSkin from '../../mixins/skin';
    import mixinsTexture from '../../mixins/texture';
    import { Texture } from '../../core';

    // const prefixCls = 'yui-bitmap';

    export default {
        name: 'bitmap1',
        mixins: [ mixinsSkin, mixinsTexture ],
        components: { ResizeObserver },
        props: {
            grids: {
                type: [Array, String, Object],
                default () {
                    return [];
                }
            },
            autosize: {
                type: [String, Boolean],
                default: false
            },
            slotClass: {
                type: String
            },
        },
        data() {
            return {
                gridSource: [],
                innerW: 0,
                innerH: 0,
                showSlot: true,
                styleWidth: 'auto',
                styleHeight: 'auto'
            };
        },
        computed: {
            styles() {
                const style = {};

                style.width = this.styleWidth;
                style.height = this.styleHeight;
                return style;
            },
            slotStyles() {
                const style = {};

                style.width = this.styleWidth;
                style.height = this.styleHeight;
                this.color && (style.color = this.color);
                this.backgroundColor && (style.backgroundColor = this.backgroundColor);
                this.textAlign && (style.textAlign = this.textAlign);
                return style;
            },
            canResize () {
                let { autosize } = this;
                autosize = ((typeof autosize === 'string') && (autosize === 'true')) || autosize;
                return autosize;
            }
        },
        methods: {
            start() {
                let { width, height } = this;

                if (typeof width === 'string') {
                    this.styleWidth = width || 'auto';
                    ((width === '100%') || (width === 'auto')) && (width = '');
                } else {
                    this.styleWidth = `${width}px`;
                }
                if (typeof height === 'string') {
                    this.styleHeight = height || 'auto';
                    ((height === '100%') || (height === 'auto')) && (height = '');
                } else {
                    this.styleHeight = `${height}px`;
                }

                this.innerW = parseFloat(width || 0.0);
                this.innerH = parseFloat(height || 0.0);

                this.gridSource = this.gridsVal(this.grids);
                this.showSlot = this.$slots.default !== undefined;
            },
            readyGrid(source) {
                if (!source) {
                    return;
                }
                this.loadGrids(source, this.gridSource, this.innerW, this.innerH, (texture) => {
                    this.reload(texture);
                });
            },
            reload(source) {
                if (!source) {
                    return;
                }
                if (source instanceof Texture) {
                    this.load(source, (texture) => {
                        this.url = (texture && texture.valid && texture.dataURL) || '';
                    });
                } else {
                    this.url = source;
                }
            },
            changed() {
                if (!this.skin || (this.skin === 'none')) {
                    this.url = '';
                    return;
                }
                this.skin && ((this.gridSource.length < 2) ? this.reload(this.skin) : this.readyGrid(this.skin));
            },
            handleClicked (event) {
                this.$emit('on-click', event);
            },
            handleMouseenter(event) {
                this.$emit('on-mouseenter', event);
            },
            handleMouseOver(event) {
                this.$emit('on-mouseover', event);
            },
            handleMouseLeave(event) {
                this.$emit('on-mouseleave', event);
            },
            handleMouseDown(event) {
                this.$emit('on-mousedown', event);
            },
            handleMouseUp(event) {
                this.$emit('on-mouseup', event);
            },
            handleResize(width, height) {
                if (!this.canResize) {
                    return;
                }

                if (this.innerW !== width || this.innerH !== height) {
                    this.innerW = width;
                    this.innerH = height;
                    this.styleWidth = `${width}px`;
                    this.styleHeight = `${height}px`;
                    this.changed();
                    this.$emit('on-resize', {width, height});
                }
            },
        },
        watch: {
            grids() {
                this.gridSource = this.gridsVal(this.grids);
                this.changed();
            },
            width(val) {
                let width = val;
                if (typeof width === 'string') {
                    this.styleWidth = width || 'auto';
                    ((width === '100%') || (width === 'auto')) && (width = '');
                } else {
                    this.styleWidth = `${width}px`;
                }
                this.innerW = parseFloat(width || 0.0);

                this.changed();
            },
            height(val) {
                let height = val;
                if (typeof height === 'string') {
                    this.styleHeight = height || 'auto';
                    ((height === '100%') || (height === 'auto')) && (height = '');
                } else {
                    this.styleHeight = `${height}px`;
                }
                this.innerH = parseFloat(height || 0.0);

                this.changed();
            },
        }
    };
</script>



