import Dispatch from './Dispatch';

export default class Thread extends Dispatch {
    constructor(scripts) {
        super();
        this._scripts = scripts;
        this._worker = null;
        this._url = null;
    }

    init() {
        super.init();
    }

    destroy() {
        super.destroy();
        this._scripts = null;
        this._worker && this._worker.terminate();
        this._url && window.URL.revokeObjectURL(this._url);
        this._worker = null;
        this._url = null;
    }

    start() {
        !this._worker && (this._worker = this._initWorker());
        super.start();
    }

    stop() {
        super.stop();
        this._worker && this._worker.terminate();
        this._worker = null;
    }

    send(data) {
        this._worker && this._worker.postMessage(data);
    }

    get running() {
        return this._worker;
    }

    _initWorker() {
        if (!this._scripts) {
            return;
        }
        if (!this._url) {
            var blob = new Blob([this._scripts],
                {type: 'application/javascript'});
            this._url = window.URL.createObjectURL(blob);
        }
        var worker = new Worker(this._url);
        var self = this;
        worker.onmessage = (e) => {
            self._onMessage(e);
        };
        worker.onerror = (e) => {
            self._onError(e);
        };
        return worker;
    }

    _onMessage(e) {
        this.emit(Thread.ONMESSAGE, e.data);
    }

    _onError(e) {
        this.emit(Thread.ONERROR, e.data);
    }

    static get ONMESSAGE() { return 'Thread.ONMESSAGE'; }
    static get ONERROR() { return 'Thread.ONERROR'; }
}



