import Rectangle from './Rectangle';
import { SHAPES, Shape } from './Shape';

export default class RoundedRectangle extends Shape {
    constructor(x = 0, y = 0, width = 0, height = 0, radius = 20) {
        super(SHAPES.RREC, x, y);
        this.width = width;
        this.height = height;
        this.radius = radius;
    }

    get bounds() {
        return new Rectangle(this.x, this.y, this.width, this.height);
    }

    get empty() {
        return (this.width === 0) || (this.height === 0);
    }

    clone() {
        return new RoundedRectangle(this.x, this.y, this.width, this.height, this.radius);
    }

    copy(info) {
        super.copy(info);
        this.width = info.width;
        this.height = info.height;
        this.radius = info.radius;

        return this;
    }

    contains(x, y) {
        if (this.width <= 0 || this.height <= 0) {
            return false;
        }
        if (x >= this.x && x <= this.x + this.width) {
            if (y >= this.y && y <= this.y + this.height) {
                if ((y >= this.y + this.radius && y <= this.y + this.height - this.radius)
                || (x >= this.x + this.radius && x <= this.x + this.width - this.radius)) {
                    return true;
                }
                let dx = x - (this.x + this.radius);
                let dy = y - (this.y + this.radius);
                const radius2 = this.radius * this.radius;

                if ((dx * dx) + (dy * dy) <= radius2) {
                    return true;
                }
                dx = x - (this.x + this.width - this.radius);
                if ((dx * dx) + (dy * dy) <= radius2) {
                    return true;
                }
                dy = y - (this.y + this.height - this.radius);
                if ((dx * dx) + (dy * dy) <= radius2) {
                    return true;
                }
                dx = x - (this.x + this.radius);
                if ((dx * dx) + (dy * dy) <= radius2) {
                    return true;
                }
            }
        }

        return false;
    }

    equal(value) {
        return super.equal(value) && (value.width === this.width) && (value.height === this.height) && (value.radius === this.radius);
    }
}



