<template>
    <component
        :is="tagName"
        :class="wrapClasses"
        v-bind="tagProps">
        <template v-if="type !== 'textarea'">
            <div :class="[prefixCls + '-group-prepend']" v-if="prepend" v-show="slotReady"><slot name="prepend"></slot></div>
            <i class="yui-icon" :class="['yui-icon-ios-close-circle', prefixCls + '-icon', prefixCls + '-icon-clear' , prefixCls + '-icon-normal']" v-if="clearable && currentValue" @click="handleClear"></i>
            <i class="yui-icon" :class="['yui-icon-' + icon, prefixCls + '-icon', prefixCls + '-icon-normal']" v-else-if="icon" @click="handleIconClick"></i>
            <i class="yui-icon yui-icon-ios-search" :class="[prefixCls + '-icon', prefixCls + '-icon-normal', prefixCls + '-search-icon']" v-else-if="search && enterButton === false" @click="handleSearch"></i>
            <span class="yui-input-suffix" v-else-if="showSuffix"><slot name="suffix"><i class="yui-icon" :class="['yui-icon-' + suffix]" v-if="suffix"></i></slot></span>
            <transition name="fade">
                <i class="yui-icon yui-icon-ios-loading yui-load-loop" :class="[prefixCls + '-icon', prefixCls + '-icon-validate']" v-if="!icon"></i>
            </transition>
            <input
                :id="elementId"
                :autocomplete="autocomplete"
                :spellcheck="spellcheck"
                ref="input"
                :type="type"
                :class="inputClasses"
                :placeholder="placeholder"
                :disabled="disabled"
                :maxlength="maxlength"
                :readonly="readonly"
                :name="name"
                :value="currentValue"
                :number="number"
                :autofocus="autofocus"
                @keyup.enter="handleEnter"
                @keyup="handleKeyup"
                @keypress="handleKeypress"
                @keydown="handleKeydown"
                @focus="handleFocus"
                @blur="handleBlur"
                @compositionstart="handleComposition"
                @compositionupdate="handleComposition"
                @compositionend="handleComposition"
                @input="handleInput"
                @change="handleChange"
                v-bind="inputProps">
            <div :class="[prefixCls + '-group-append']" v-if="append" v-show="slotReady"><slot name="append"></slot></div>
            <div :class="[prefixCls + '-group-append', prefixCls + '-search']" v-else-if="search && enterButton" @click="handleSearch">
                <i class="yui-icon yui-icon-ios-search" v-if="enterButton === true"></i>
                <template v-else>{{ enterButton }}</template>
            </div>
            <span class="yui-input-prefix" v-else-if="showPrefix"><slot name="prefix"><i class="yui-icon" :class="['yui-icon-' + prefix]" v-if="prefix"></i></slot></span>
        </template>
        <textarea
            v-else
            :id="elementId"
            :wrap="wrap"
            :autocomplete="autocomplete"
            :spellcheck="spellcheck"
            ref="textarea"
            :class="textareaClasses"
            :placeholder="placeholder"
            :disabled="disabled"
            :rows="rows"
            :maxlength="maxlength"
            :readonly="readonly"
            :name="name"
            :value="currentValue"
            :autofocus="autofocus"
            @keyup.enter="handleEnter"
            @keyup="handleKeyup"
            @keypress="handleKeypress"
            @keydown="handleKeydown"
            @focus="handleFocus"
            @blur="handleBlur"
            @compositionstart="handleComposition"
            @compositionupdate="handleComposition"
            @compositionend="handleComposition"
            @input="handleInput"
            v-bind="textProps">
        </textarea>
    </component>
</template>
<script>
    import Clip from '../clip';
    import { oneOf, findComponentUpward } from '../../utils/assist';
    import calcTextareaHeight from '../../utils/calcTextareaHeight';
    import Emitter from '../../mixins/emitter';
    import mixinsSkin from '../../mixins/skin';

    const prefixCls = 'yui-input';

    export default {
        name: 'Input',
        mixins: [ Emitter, mixinsSkin, ],
        components: { Clip },
        inheritAttrs: false,
        props: {
            type: {
                validator (value) {
                    return oneOf(value, ['text', 'textarea', 'password', 'url', 'email', 'date']);
                },
                default: 'text'
            },
            value: {
                type: [String, Number],
                default: ''
            },
            size: {
                validator (value) {
                    return oneOf(value, ['small', 'large', 'default']);
                },
                default () {
                    return !this.$YUI || this.$YUI.size === '' ? 'default' : this.$YUI.size;
                }
            },
            placeholder: {
                type: String,
                default: ''
            },
            maxlength: {
                type: Number
            },
            disabled: {
                type: Boolean,
                default: false
            },
            icon: String,
            autosize: {
                type: [Boolean, Object],
                default: false
            },
            rows: {
                type: Number,
                default: 2
            },
            readonly: {
                type: Boolean,
                default: false
            },
            name: {
                type: String
            },
            number: {
                type: Boolean,
                default: false
            },
            autofocus: {
                type: Boolean,
                default: false
            },
            spellcheck: {
                type: Boolean,
                default: false
            },
            autocomplete: {
                validator (value) {
                    return oneOf(value, ['on', 'off']);
                },
                default: 'off'
            },
            clearable: {
                type: Boolean,
                default: false
            },
            elementId: {
                type: String
            },
            wrap: {
                validator (value) {
                    return oneOf(value, ['hard', 'soft']);
                },
                default: 'soft'
            },
            prefix: {
                type: String,
                default: ''
            },
            suffix: {
                type: String,
                default: ''
            },
            search: {
                type: Boolean,
                default: false
            },
            enterButton: {
                type: [Boolean, String],
                default: false
            },
            rule: {
                type: [Array, String, Object]
            },
            regex: {
                type: Object
            },
            formatter: {
                type: Function
            },
            parser: {
                type: Function
            },
        },
        data () {
            return {
                currentValue: this.value,
                prefixCls: prefixCls,
                prepend: true,
                append: true,
                slotReady: false,
                textareaStyles: {},
                showPrefix: false,
                showSuffix: false,
                isOnComposition: false,
                ruleInfo: {},
            };
        },
        computed: {
            tagName() {
                const {skin} = this;
                return (skin && Clip) || 'div';
            },
            wrapClasses () {
                return [
                    `${prefixCls}-wrapper`,
                    {
                        [`${prefixCls}-wrapper-${this.size}`]: !!this.size,
                        [`${prefixCls}-type`]: this.type,
                        [`${prefixCls}-group`]: this.prepend || this.append || (this.search && this.enterButton),
                        [`${prefixCls}-group-${this.size}`]: (this.prepend || this.append || (this.search && this.enterButton)) && !!this.size,
                        [`${prefixCls}-group-with-prepend`]: this.prepend,
                        [`${prefixCls}-group-with-append`]: this.append || (this.search && this.enterButton),
                        [`${prefixCls}-hide-icon`]: this.append,  // #554
                        [`${prefixCls}-with-search`]: (this.search && this.enterButton),
                        [`${prefixCls}-define`]: !!this.skin,
                        [`${prefixCls}-resized`]: this.canresized,
                        [`${prefixCls}-fixed`]: !this.canresized,
                    }
                ];
            },
            inputClasses () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-${this.size}`]: !!this.size,
                        [`${prefixCls}-disabled`]: this.disabled,
                        [`${prefixCls}-with-prefix`]: this.showPrefix,
                        [`${prefixCls}-with-suffix`]: this.showSuffix || (this.search && this.enterButton === false),
                        [`${prefixCls}-define`]: !!this.skin,
                        [`${prefixCls}-define-input`]: !!this.height,
                        [`${prefixCls}-fixed`]: !this.canresized,
                    }
                ];
            },
            textareaClasses () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-disabled`]: this.disabled,
                        [`${prefixCls}-define`]: !!this.skin,
                        [`${prefixCls}-resized`]: this.canresized,
                        [`${prefixCls}-define-input`]: !!this.height,
                        [`${prefixCls}-fixed`]: !this.canresized,
                    }
                ];
            },
            tagProps() {
                const autosize = (this.canresized && 'true') || 'false';
                const props = { autosize };
                const {skin} = this;
                const style = { };
                if (!this.canresized) {
                    style.width = this.width !== undefined && this.width || 'auto';
                    style.height = this.height !== undefined && this.height || 'auto';
                    props.style = style;
                }
                if (skin) {
                    props.mouseEnable = 'true';
                    this.url && (props.skin = this.url);
                    if (this.ruleInfo) {
                        this.ruleInfo.clips && (props.clips = this.ruleInfo.clips);
                        this.ruleInfo.grids && (props.grids = this.ruleInfo.grids);
                        this.ruleInfo.frame && (props.frame = parseInt(this.ruleInfo.frame));
                    }
                    if (!this.canresized) {
                        this.width && (props.width = this.width);
                        this.height && (props.height = this.height);
                    }
                }
                this.color && (props.color = this.color);
                this.backgroundColor && (props.backgroundColor = this.backgroundColor);
                this.textAlign && (props.textAlign = this.textAlign);

                return props;
            },
            canresized() {
                return this.autosize && this.type === 'textarea';
            },
            inputProps() {
                const style = {};
                this.color && (style.color = this.color);
                this.backgroundColor && (style.backgroundColor = this.backgroundColor);
                this.textAlign && (style.textAlign = this.textAlign);

                return Object.assign({}, { style }, this.$attrs);
            },
            textProps() {
                const style = this.textareaStyles || {};
                this.color && (style.color = this.color);
                this.backgroundColor && (style.backgroundColor = this.backgroundColor);
                this.textAlign && (style.textAlign = this.textAlign);

                return Object.assign({}, { style }, this.$attrs);
            },
            formatterValue () {
                return (this.formatter && this.formatter(this.currentValue)) || this.currentValue;
            }
        },
        methods: {
            handleEnter (event) {
                this.$emit('on-enter', event);
                if (this.search) this.$emit('on-search', this.currentValue);
            },
            handleKeydown (event) {
                this.$emit('on-keydown', event);
            },
            handleKeypress(event) {
                this.$emit('on-keypress', event);
            },
            handleKeyup (event) {
                this.$emit('on-keyup', event);
            },
            handleIconClick (event) {
                this.$emit('on-click', event);
            },
            handleFocus (event) {
                this.$emit('on-focus', event);
            },
            handleBlur (event) {
                this.$emit('on-blur', event);
                if (!findComponentUpward(this, ['DatePicker', 'TimePicker', 'Cascader', 'Search'])) {
                    this.dispatch('FormItem', 'on-form-blur', this.currentValue);
                }
            },
            handleComposition(event) {
                if (event.type === 'compositionstart') {
                    this.isOnComposition = true;
                }
                if (event.type === 'compositionend') {
                    this.isOnComposition = false;
                    this.handleInput(event);
                }
            },
            handleInput (event) {
                if (this.isOnComposition) return;

                let value = event.target.value;
                let val = this.parserValue(value);
                if (value !== val ) {
                    value = val;
                    event.target.value = val;
                }
                this.$emit('input', value);
                this.setCurrentValue(value);
                this.$emit('on-change', event);
            },
            handleChange (event) {
                this.$emit('on-input-change', event);
            },
            setCurrentValue (value) {
                if (value === this.currentValue) return;
                this.$nextTick(() => {
                    this.resizeTextarea();
                });
                this.currentValue = value;
                if (!findComponentUpward(this, ['DatePicker', 'TimePicker', 'Cascader', 'Search'])) {
                    this.dispatch('FormItem', 'on-form-change', value);
                }
            },
            resizeTextarea () {
                const autosize = this.autosize;
                if (!autosize || this.type !== 'textarea') {
                    return false;
                }

                const minRows = autosize.minRows;
                const maxRows = autosize.maxRows;

                this.textareaStyles = calcTextareaHeight(this.$refs.textarea, minRows, maxRows);
            },
            focus () {
                if (this.type === 'textarea') {
                    this.$refs.textarea.focus();
                } else {
                    this.$refs.input.focus();
                }
            },
            blur () {
                if (this.type === 'textarea') {
                    this.$refs.textarea.blur();
                } else {
                    this.$refs.input.blur();
                }
            },
            handleClear () {
                const e = { target: { value: '' } };
                this.$emit('input', '');
                this.setCurrentValue('');
                this.$emit('on-change', e);
            },
            handleSearch () {
                if (this.disabled) return false;
                this.$refs.input.focus();
                this.$emit('on-search', this.currentValue);
            },
            start() {
                this.ruleInfo = this.reloadSource();
            },
            changed() {
                if (this.type !== 'textarea') {
                    this.prepend = this.$slots.prepend !== undefined;
                    this.append = this.$slots.append !== undefined;
                    this.showPrefix = this.prefix !== '' || this.$slots.prefix !== undefined;
                    this.showSuffix = this.suffix !== '' || this.$slots.suffix !== undefined;
                } else {
                    this.prepend = false;
                    this.append = false;
                }
                this.slotReady = true;
                this.resizeTextarea();

                this.url = ((this.skin !== 'none') && this.skin) || '';
            },
            reloadSource() {
                var result = {};
                var value = this.rule || {};
                if (typeof value === 'string') {
                    value = value.split('||') || [];
                }
                if (value instanceof Array) {
                    (value.length > 0) && (result.clips = value[0]);
                    (value.length > 1) && (result.grids = value[1]);
                    (value.length > 2) && (result.frame = value[2]);
                } else {
                    const {clips, grids, frame} = value;
                    result = { clips, grids, frame };
                }
                return result;
            },
            parserValue(value) {
                var result = value || '';
                const reg = this.number ? /[^\d.]/g : this.regex;
                if (result && reg) {
                    result = String(result).replace(reg);
                    if (this.number && result) {
                        var val = result.trim();
                        val =  Number(val);
                        result = !isNaN(val) ? val : '';
                    }
                }
                if (this.parser) {
                    result = this.parser(result);
                }
                return result;
            }
        },
        watch: {
            value (val) {
                val = this.parserValue(val);
                this.setCurrentValue(val);
            },
            rule() {
                this.ruleInfo = this.reloadSource();
                this.changed();
            }
        },
    };
</script>



