/**
 * https://github.com/freeze-component/vue-popper
 * */
import Vue from 'vue';
import Emitter from '../../mixins/emitter';
const isServer = Vue.prototype.$isServer;
const Popper = isServer ? function() {} : require('popper.js/dist/umd/popper.js');  // eslint-disable-line
const GlobalExclusives = {};

export default {
    mixins: [ Emitter ],
    props: {
        placement: {
            type: String,
            default: 'bottom'
        },
        boundariesPadding: {
            type: Number,
            default: 5
        },
        reference: Object,
        popper: Object,
        offset: {
            default: 0
        },
        value: {
            type: Boolean,
            default: false
        },
        groupId: {
            type: [String, Number],
            default: null
        },
        transition: String,
        options: {
            type: Object,
            default () {
                return {
                    modifiers: {
                        computeStyle:{
                            gpuAcceleration: false,
                        },
                        preventOverflow :{
                            boundariesElement: 'window'
                        }
                    }
                };
            }
        },
        disabled: {
            type: Boolean,
            default: false
        },
        manual: {
            type: Object,
            default: null
        }
    },
    data () {
        return {
            visible: this.value
        };
    },
    watch: {
        value: {
            immediate: true,
            handler(val) {
                this.visible = val;
                this.$emit('input', val);
            }
        },
        visible(val) {
            if (this.disabled) {
                return;
            }
            if (this.groupId) {
                GlobalExclusives[this.groupId] = GlobalExclusives[this.groupId] || [];
                const groups = GlobalExclusives[this.groupId];
                const index = groups.indexOf(this);
                if (index > -1) {
                    groups.splice(index, 1);
                }
            }
            if (val) {
                if (this.handleIndexIncrease) this.handleIndexIncrease();  // just use for Poptip
                if (this.groupId) {
                    GlobalExclusives[this.groupId] = GlobalExclusives[this.groupId] || [];
                    const groups = GlobalExclusives[this.groupId];

                    groups.forEach((vm)=>{
                        vm && (vm !== this) && !vm.always && (vm.visible = false);
                    });
                    groups.push(this);
                }
                this.updatePopper();
                this.$emit('on-popper-show');
            } else {
                this.$emit('on-popper-hide');
            }
            this.$emit('input', val);
        },
        disabled() {
            this.$emit('on-popper-hide');
        },
        // 手动控制
        manual() {
            this.createPopper();
        }
    },
    methods: {
        createPopper() {
            if (isServer) return;
            if (!/^(top|bottom|left|right)(-start|-end)?$/g.test(this.placement)) {
                return;
            }
            var { options, popper, reference } = this.manual || {};
            options = options || this.options;
            popper = popper || this.popper || this.$refs.popper;
            reference = reference || this.reference || this.$refs.reference;

            if (!popper || !reference) return;

            if (this.popperJS && this.popperJS.hasOwnProperty('destroy')) {
                this.popperJS.destroy();
            }

            options.placement = this.placement;

            if (!options.modifiers.offset) {
                options.modifiers.offset = {};
            }
            options.modifiers.offset.offset = this.offset;
            options.onCreate =()=>{
                this.$nextTick(this.updatePopper);
                this.$emit('created', this);
            };

            this.popperJS = new Popper(reference, popper, options);
        },
        updatePopper() {
            if (isServer) return;
            this.popperJS ? this.popperJS.update() : this.createPopper();
        },
        doDestroy() {
            if (isServer) return;
            if (this.visible) return;
            this.popperJS.destroy();
            this.popperJS = null;
        }
    },
    updated (){
        this.$nextTick(()=>this.updatePopper());
    },
    beforeDestroy() {
        if (isServer) return;
        if (this.popperJS) {
            this.popperJS.destroy();
        }
    }
};



