import Base from './Base';
import App from '../App';

export default class Module extends Base {
    constructor() {
        super();
        this._stoped = false;
    }

    init() {
        this._stoped = false;
    }

    start() {
        this._stoped = false;
    }

    stop() {
        this._stoped = true;
    }

    destroy() {
        this._stoped = true;
    }

    get stoped() {
        return this._stoped;
    }

    callLater(method) {
        !this._stoped && App.timers.doOnce(100, method);
    }

    removeLater(method) {
        App.timers.clearTimer(method);
    }
}



