export default {
    methods: {
        ui_position (position, width, height, left, top) {
            return this.ui_css({ position, width, height, left, top });
        },

        ui_size (width, height) {
            return this.ui_css({ width, height });
        },

        ui_background (image) {
            var style = {};
            if (image) {
                style['background-image'] = `url(${image})`;
                style['background-repeat'] = 'no-repeat';
                style['background-position'] = 'center';
            }
            return this.ui_css(style);
        },

        ui_css (style) {
            var result = '';
            if (typeof style !== 'object') {
                return result;
            }
            for (var key in style) {
                var value = style[key];
                var type = typeof value;
                if (type === 'number') {
                    value = `${value}px`;
                    type = 'string';
                }
                if (type === 'string') {
                    value && (result += `${key}:${value};`);
                }
            }
            return result;
        },

        ui_csses () {
            var result = '';
            const args = arguments;
            for (var i = 0; i < args.length; ++i) {
                var value = args[i];
                var type = typeof value;
                if (type === 'object') {
                    value = this.ui_css(value);
                    type = 'string';
                }
                if (type === 'string') {
                    value && (result += value);
                }
            }
            return result;
        },
    }
};



