import * as uis from './components';
import * as locale from './locale';
import * as core from './core/index';

const yui = {
    ...uis
};

const install = function(Vue, opts = {}) {
    if (install.installed) return;
    locale.use(opts.locale);
    locale.i18n(opts.i18n);

    Object.keys(yui).forEach(key => {
        Vue.component(key, yui[key]);
    });

    Vue.prototype.$YUI = {
        size: opts.size || '',
        dev: 'dev' in opts ? opts.dev : true,
        transfer: 'transfer' in opts ? opts.transfer : ''
    };
    core.App.init();
    Vue.prototype.$Loading = uis.ULoadingBar;
    Vue.prototype.$Message = uis.UMessage;
    Vue.prototype.$Modal = uis.UModal;
    Vue.prototype.$Notice = uis.UNotice;
    Vue.prototype.$Spin = uis.USpin;
    Vue.prototype.$Core = core;
    core.App.start();

    install.installed = true;
};

// auto install
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

const API = {
    version: process.env.VERSION, // eslint-disable-line no-undef
    locale: locale.use,
    i18n: locale.i18n,
    install,
    ...uis,
    ...core
};

API.lang = (code) => {
    const langObject = window['yui/locale'].default;
    if (code === langObject.i.locale) locale.use(langObject);
    else console.log(`The ${code} language pack is not loaded.`); // eslint-disable-line no-console
};

export default API;
// module.exports.default = module.exports = API;   // eslint-disable-line no-undef



