<template>
    <component
        :is="tagName"
        :class="classes"
        @click="handleClick"
        v-bind="tagProps"
    >

    </component>
</template>
<script>
    import mixinsSkin from '../../mixins/skin';

    const prefixCls = 'yui-icon';

    export default {
        name: 'Icon',
        mixins: [ mixinsSkin ],
        props: {
            type: {
                type: String,
                default: ''
            },
            size: [Number, String],
            color: String,
            custom: {
                type: String,
                default: ''
            }
        },
        computed: {
            tagName() {
                const { skin } = this;
                return !skin ? 'i' : 'img';
            },
            classes () {
                return [
                    `${prefixCls}`,
                    {
                        [`${prefixCls}-${this.type}`]: (this.type !== '') && !this.skin,
                        [`${this.custom}`]: this.custom !== '',
                    }
                ];
            },
            tagProps() {
                const style = {};
                const props = {};

                if (this.size) {
                    style['font-size'] = `${this.size}px`;
                }

                if (this.color) {
                    style.color = this.color;
                }
                this.width && (style.width = this.width);
                this.height && (style.height = this.height);

                props.style = style;
                this.url && (props.src = this.url);

                return props;
            }
        },
        methods: {
            handleClick (event) {
                this.$emit('click', event);
            },
            changed() {
                if (!this.skin || (this.skin === 'none')) {
                    this.url = '';
                    return;
                }
                this.url = this.skin;
            },
        }
    };
</script>



