<template>
    <div
        class="yui-container"
        @click="handleClicked"
        @mouseenter="handleMouseenter"
        @mouseover="handleMouseOver"
        @mouseleave="handleMouseLeave"
        @mousedown="handleMouseDown"
        @mouseup="handleMouseUp"
        :style="styles"
        >
        <div class="yui-container-back"><slot name="background"></slot></div>
        <div class="yui-container-face"><slot name="foreground"></slot></div>
        <resize-observer v-if="autoWidth || autoHeight" @on-resize="handleResize" />
    </div>
</template>
<script>
    import ResizeObserver from '../base/resize';
    import mixinsSkin from '../../mixins/skin';

    export default {
        name: 'container',
        mixins: [ mixinsSkin ],
        components: { ResizeObserver },
        data() {
            return {
                innerW: 0,
                innerH: 0,
                autoWidth: false,
                autoHeight: false
            };
        },
        computed: {
            styles() {
                const style = {};

                style.width = (this.autoWidth && '100%') || (this.innerW && `${this.innerW}px`) || 'auto';
                style.height = (this.autoHeight && '100%') || (this.innerH && `${this.innerH}px`) || 'auto';
                this.color && (style.color = this.color);
                this.backgroundColor && (style.backgroundColor = this.backgroundColor);
                this.textAlign && (style.textAlign = this.textAlign);
                return style;
            },
        },
        methods: {
            start() {
                this.innerW = parseFloat(this.width || 0);
                this.innerH = parseFloat(this.height || 0);
                this.autoWidth = (this.width === '100%' || !this.innerW);
                this.autoHeight = (this.height === '100%' || !this.innerH);
            },
            handleResize(width, height) {
                if (this.innerW !== width || this.innerH !== height) {
                    this.innerW = width;
                    this.innerH = height;
                    this.$emit('on-resize', {width, height});
                }
            },
            handleClicked (event) {
                this.$emit('on-click', event);
            },
            handleMouseenter(event) {
                this.$emit('on-mouseenter', event);
            },
            handleMouseOver(event) {
                this.$emit('on-mouseover', event);
            },
            handleMouseLeave(event) {
                this.$emit('on-mouseleave', event);
            },
            handleMouseDown(event) {
                this.$emit('on-mousedown', event);
            },
            handleMouseUp(event) {
                this.$emit('on-mouseup', event);
            },
        },
        watch: {
            width() {
                this.innerW = parseFloat(this.width || 0);
                this.autoWidth = (this.width === '100%' || !this.innerW);
            },
            height() {
                this.innerH = parseFloat(this.height || 0);
                this.autoHeight = (this.height === '100%' || !this.innerH);
            },
        }
    };
</script>



