export class ResParam {
    constructor(id, url, type, data = null, cache = false) {
        this.id = id;
        this.data = data;
        this.type = type;
        this.url = url;
        this.cache = cache;
    }
}

export class ResTask {
    static get ADD() { return 'add'; }

    static get OK() { return 0; }
    static get ERROR() { return 1; }
    static get TIMEOUT() { return 2; }
    static get PROGRESS() { return 3; }
}

ResTask.Scripts =
`
const POST = 'POST';
const GET = 'GET';

class Request {
    constructor() {
        this.timeout = 4000;

        this._xhr = new XMLHttpRequest();
        this._xhr.onreadystatechange = (e) => {
            this._onReadyStateChange(e);
        };
        this._xhr.ontimeout = (e) => {
            this._onTimeOut(e);
        };
        this._xhr.onprogress = (e) => {
            this._onProgress(e);
        };
        this._headers = [];
        this._extra = null;

        this.oncompleted = null;
        this.onprogress = null;
        this.ontimeout = null;
        this.onerror = null;
    }

    get(url, data = null, extra = null, type = Request.TEXT) {
        this._extra = extra;
        this._doRequest(GET, url, data, type);
    }

    post(url, data = null, extra = null, type = Request.TEXT) {
        this._extra = extra;
        this._doRequest(POST, url, data, type);
    }

    stop() {
        this._xhr.abort();
    }

    destory() {
        this.oncompleted = null;
        this.onprogress = null;
        this.ontimeout = null;
        this.onerror = null;

        this._xhr.onreadystatechange = null;
        this._xhr.ontimeout = null;
        this._xhr = null;
    }

    set headers(value) {
        this._headers = value;
    }

    get header() {
        this._xhr.getAllResponseHeaders();
    }

    _doRequest(type, url, data, dataType) {
        if ((this._xhr.readyState !== this._xhr.DONE) && (this._xhr.readyState !== this._xhr.UNSENT)) {
            this._xhr.abort();
        }

        this._xhr.open(type, url, false);

        this._xhr.responseType = dataType;
        this._xhr.timeout = this._timeout;

        for (var index in this._headers) {
            var header = this._headers[index];
            this._xhr.setRequestHeader(header.name, header.value);
        }
        this._xhr.send(data);
    }

    _onReadyStateChange(e) {
        if (this._xhr.readyState !== this._xhr.DONE) {
            return;
        }
        if (this._xhr.status === 200) {
            this.oncompleted && this.oncompleted(this._extra, this._xhr.responseType, this._xhr.response);
        } else {
            this.onerror && this.onerror(this._extra, this._xhr.readyState, this._xhr.status);
        }
    }

    _onTimeOut() {
        this._xhr.abort();
        this.ontimeout && this.ontimeout(this._extra);
    }

    _onProgress(e) {
        this.onprogress && this.onprogress(this._extra, e.loaded, e.total);
    }

    static get IMAGE() { return 'image'; }
    static get TEXT() { return 'text'; }
    static get XML() { return 'xml'; }
    static get DOCUMENT() { return 'document'; }
    static get JSON() { return 'json'; }
    static get FONT() { return 'font'; }
    static get SVG() { return 'svg'; }
}

class Runner {
    constructor() {
        this._req = new Request();
        var self = this;
        this._req.oncompleted = (extra, responseType, response) => {
            self._onCompleted(extra, responseType, response);
        }
        this._req.onprogress = (extra, loaded, total) => {
            self._onProgress(extra, loaded, total);
        }
        this._req.ontimeout = (extra) => {
            self._onTimeOut(extra);
        }
        this._req.onerror = (extra, readyState, status) => {
            self._onError(extra, readyState, status);
        }
        this._queue = [];
    }

    send(data) {
        postMessage(data);
    }

    onWorking(e) {
        var req = e.data;
        if (req && req.param) {
            switch (req.uri) {
                case '${ResTask.ADD}':
                    var param = req.param;
                    var url = param.url;
                    var data = param.data;
                    var type = param.type;
                    this._queue.push({param, url, data, type});
                    this._nextRequest();
                    break;
                default: break;
            }
        }
    }

    destroy() {
        this._queue = [];
        this._req.destory();
        super.destroy();
    }

    _onCompleted(param, type, data) {
        var code = ${ResTask.OK};
        data = {type, data};
        this.send({param, code, data});

        this._nextRequest();
    }

    _onError(param, state, status) {
        var code = ${ResTask.ERROR};
        var data = {state, status};
        this.send({param, code, data});
        this._nextRequest();
    }

    _onProgress(param, loaded, total) {
        var code = ${ResTask.PROGRESS};
        var data = {loaded, total};
        this.send({param, code, data});
    }

    _onTimeOut(param) {
        var code = ${ResTask.TIMEOUT};
        this.send({param, code});
        this._nextRequest();
    }

    _nextRequest() {
        if (this._queue.length > 0) {
            var task = this._queue.shift();
            task && this._req.post(task.url, task.data, task.param, task.type);
        }
    }
}

const runner = new Runner();
const MESSAGE = 'message';

addEventListener(MESSAGE, (e) => {
    runner && runner.onWorking(e);
}, false);
`
;



