import { Module, Loader } from '../base';

class ResTask {
    constructor(url, type, onComplete, onError, onProgress, isCache) {
        this.url = url;
        this.type = type;
        this.onComplete = onComplete;
        this.onError = onError;
        this.onProgress = onProgress;
        this.isCache = isCache;
        this.errorCount = 0;
    }

    clear() {
        this.onComplete = null;
        this.onError = null;
        this.onProgress = null;
    }
}

export default class Loaders extends Module {
    constructor() {
        super();
        this._retryCount = 1;
        this._tasks = new Map();
        this._loader = new Loader();
    }

    init() {
        super.init();
        var self = this;
        this._loader.on(Loader.ONCOMPLETED, (e) => {
            self._onCompleted(e);
        });
        this._loader.on(Loader.ONPROGRESS, (e) => {
            self._onProgress(e);
        });
        this._loader.on(Loader.ONERROR, (e) => {
            self._onError(e);
        });
    }

    start() {
        super.start();
        this._loader.start();
    }

    stop() {
        this._loader.stop();
        super.stop();
    }

    destroy() {
        this._loader.destroy();
        super.destroy();
    }

    getRes(url) {
        return Loader.getRes(url);
    }

    setRes(url, content) {
        Loader.setRes(url, content);
    }

    clearRes(url) {
        Loader.clearRes(url);
    }

    clearAll() {
        Loader.clearAll();
    }

    load(source, type, onComplete = null, onError = null, onProgress = null, isCache = true) {
        source = (typeof (source) === 'string') ? [source] : source;
        for (var index in source) {
            var url = source[index];
            var content = Loader.getRes(url);
            var task = new ResTask(url, type, onComplete, onError, onProgress, isCache);
            if (content) {
                task.onComplete && task.onComplete(url, content);
            } else {
                this._startTask(task);
            }
        }
    }

    _onCompleted(e) {
        var id = e.id;
        var task = this._tasks.get(id);
        if (task) {
            task.onComplete && task.onComplete(e.url, e.data);
            task.clear();
        }
        this._tasks.delete(id);
    }

    _onProgress(e) {
        var id = e.id;
        var task = this._tasks.get(id);
        if (task) {
            task.onProgress && task.onProgress(e.url, e.loaded, e.total);
        }
    }

    _onError(e) {
        var id = e.id;
        var task = this._tasks.get(id);
        if (task) {
            this._tasks.delete(id);
            if (task.errorCount < this._retryCount) {
                ++task.errorCount;
                this._startTask(task);
            } else {
                task.onError && task.onError(task.url);
            }
        }
    }

    _startTask(task) {
        var id = this._loader.load(task.url, task.type, task.isCache);
        this._tasks.set(id, task);
    }
}



