import { Module } from './base';
import { Assets, Loaders, Timers } from './manager';

class App extends Module {
    constructor() {
        super();
        this.timers = new Timers();
        this.loaders = new Loaders();
        this.assets = new Assets();
    }

    init() {
        super.init();
        this.timers.init();
        this.loaders.init();
        this.assets.init();
        return true;
    }

    start() {
        super.start();
        this.timers.start();
        this.loaders.start();
        this.assets.start();
    }

    stop() {
        super.stop();
        this.timers.stop();
        this.loaders.stop();
        this.assets.stop();
    }

    destroy() {
        super.destroy();
        this.timers.destroy();
        this.loaders.destroy();
        this.assets.destroy();
    }
    static __impl() {
        return App.__instance || (App.__instance = new App());
    }
}

App.__instance = null;

export default {
    // impl: App.__impl(),
    init() {
        App.__impl().init();
    },
    destroy() {
        App.__impl().destroy();
    },
    start() {
        App.__impl().start();
    },
    stop() {
        App.__impl().stop();
    },
    timers() {
        return App.__impl().timers;
    },
    loaders() {
        return App.__impl().loaders;
    },
    assets() {
        return App.__impl().assets;
    }
};



