<template>
    <div
        :class="[prefixCls + '-body']"
        @scroll="handleListScroll"
        @mousewheel="handleMouseWheel"
        v-bind="tagProps">
        <div class="yui-list-body-container" :style="containerStyle">
            <div class="yui-list-body-container-view" :style="viewStyle">
                <template v-for="(item, index) in viewData">
                    <list-item
                        class="yui-list-body-item"
                        :prefix-cls="prefixCls"
                        :item="item"
                        :key="startIndex + index"
                        :index="startIndex + index"
                        :option="option"
                        :width="itemWidth"
                        :height="itemHeight"
                        :selected="rowSelected(startIndex + index)"
                        :hovered="rowHovered(startIndex + index)"
                        @mouseenter.native.stop="handleMouseIn(startIndex + index)"
                        @mouseleave.native.stop="handleMouseOut(startIndex + index)"
                        @click.native="clickCurrentRow(startIndex + index)"
                        @dblclick.native.stop="dblclickCurrentRow(startIndex + index)"
                        ></list-item>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
    import ListItem from './item.vue';

    const kBufferSize = 5;

    export default {
        name: 'ListBody',
        components: { ListItem },
        props: {
            prefixCls: String,
            data: Array,    // rebuildData
            size: [Object, Array, String],
            option: Object,
            objData: Object,
            direction: String
        },
        data () {
            return {
                itemWidth: 0,
                itemHeight: 0,
                width: 0,
                height: 0,
                viewData: [],
                startIndex: null,
                viewOffset: 0,
                scrollOffset: 0,
                hoverIndex: null,
                selectedIndex: null,
            };
        },
        computed: {
            tagProps () {
                const style = {
                    display: 'flex',
                    flexDirection: this.direction || 'row',
                    overflowY: (this.direction === 'column') && 'auto' || 'hidden',
                    overflowX: (this.direction === 'row') && 'auto' || 'hidden',
                };

                return { style };
            },
            containerStyle () {
                const totalCount = this.data ? this.data.length : 0;
                const itemSize = (this.direction === 'row') ? this.itemWidth : this.itemHeight;
                const totalSize = itemSize * totalCount;

                return {
                    display: 'flex',
                    flexDirection: this.direction || 'row',
                    width: (this.direction === 'row') ? `${totalSize}px` : `${this.width}px`,
                    height: (this.direction === 'row') ? `${this.height}px` : `${totalSize}px`
                };
            },
            viewStyle () {
                const itemSize = (this.direction === 'row') ? this.itemWidth : this.itemHeight;
                const viewSize = this.viewData.length * itemSize;
                return {
                    display: 'flex',
                    flexDirection: this.direction || 'row',
                    top: (this.direction === 'row') ? '0px' : `${this.viewOffset}px`,
                    left: (this.direction === 'row') ? `${this.viewOffset}px` : '0px',
                    width: (this.direction === 'row') ? `${viewSize}px` : `${this.width}px`,
                    height: (this.direction === 'row') ? `${this.height}px` : `${viewSize}px`
                };
            }
        },
        mounted () {
            this.resize(this.$el.offsetWidth, this.$el.offsetHeight);
            this.loadItemSize(this.size);
            this.scrollListView();
        },
        methods: {
            rowSelected (index) {
                return this.selectedIndex === index;
            },
            rowHovered (index) {
                return this.hoverIndex === index;
            },
            handleMouseIn (index) {
                if (this.hoverIndex !== index) {
                    this.hoverIndex = index;
                    this.$parent.$parent.handleItemHovered(index);
                }
            },
            handleMouseOut () {
                this.hoverIndex = null;
            },
            clickCurrentRow (index) {
                this.$parent.$parent.handleItemClicked(index);
                if (this.selectedIndex !== index) {
                    this.selectedIndex = index;
                    this.$parent.$parent.handleItemSelected(index);
                }
            },
            dblclickCurrentRow (index) {
                this.$parent.$parent.handleItemDbClicked(index);
                if (this.selectedIndex !== index) {
                    this.selectedIndex = index;
                    this.$parent.$parent.handleItemSelected(index);
                }
            },
            handleListScroll (e) {
                event.stopPropagation();
                this.scrollListView(e);
            },
            handleMouseWheel (e) {
                if (this.direction === 'row') {
                    const deltaY = e.deltaY;
                    this.$el.scrollLeft += deltaY;
                }
            },
            loadItemSize (value) {
                var result = {};
                value = value || {};
                if (typeof value === 'string') {
                    value = value.split(' ') || [];
                }
                if (value instanceof Array) {
                    (value.length > 0) && (result.width = value[0]);
                    (value.length > 1) && (result.height = value[1]);
                }

                this.itemWidth = parseFloat(result.width || 0);
                this.itemHeight = parseFloat(result.height || 0);
                this.itemSize = (this.direction === 'row') ? this.itemWidth : this.itemHeight;
            },
            rebuildData (offset) {
                const itemSize = (this.direction === 'row') ? this.itemWidth : this.itemHeight;
                if (itemSize <= 0 || this.data.length <= 0) {
                    this.reset();
                    return;
                }
                if (offset < 0) {
                    offset = 0;
                }

                const offsetCount = Math.floor(offset / itemSize);
                this.viewOffset = offsetCount * itemSize;
                const startIndex = offsetCount > 0 ? (offsetCount - 1) : 0;
                if (this.startIndex !== startIndex) {
                    this.startIndex = startIndex;
                    const screenSize = (this.direction === 'row') ? this.width : this.height;
                    let endIndex = Math.ceil(screenSize / itemSize) + startIndex + kBufferSize;
                    this.viewData = this.data.slice(this.startIndex, endIndex);
                }
            },
            scrollListView () {
                if (!this.data || !this.data.length) {
                    this.reset();
                    return;
                }
                const itemSize = (this.direction === 'row') ? this.itemWidth : this.itemHeight;
                const screenSize = (this.direction === 'row') ? this.width : this.height;
                const viewSize = this.viewData.length * itemSize;
                const scrollOffset = (this.direction === 'row') ? this.$el.scrollLeft : this.$el.scrollTop;
                if ((scrollOffset >= this.scrollOffset) && (this.viewOffset + viewSize < scrollOffset + screenSize)) {
                    this.rebuildData(scrollOffset);
                } else if ((scrollOffset < this.scrollOffset) && (this.viewOffset >= scrollOffset)){
                    this.rebuildData(scrollOffset - itemSize * kBufferSize);
                }
                this.scrollOffset = scrollOffset;
            },
            resize (width, height) {
                this.width = width;
                this.height = height;
            },
            reset() {
                this.startIndex = 0;
                this.viewData = [];
                this.viewOffset = null;
            }
        },
        watch: {
            size(val) {
                this.loadItemSize(val);
            },
            data(val) {
                this.scrollListView(val);
            }
        }
    };
</script>



