<template>
    <li
        :class="classes"
        @click.stop="select"
        @mousedown.prevent
        v-on="eventProps"
        :style="styles"
    ><slot>{{ showLabel }}</slot></li>
</template>
<script>
    import Emitter from '../../mixins/emitter';
    import { findComponentUpward } from '../../utils/assist';

    const prefixCls = 'yui-select-item';

    export default {
        name: 'uOption',
        componentName: 'select-item',
        mixins: [ Emitter ],
        props: {
            value: {
                type: [String, Number],
                required: true
            },
            label: {
                type: [String, Number]
            },
            disabled: {
                type: Boolean,
                default: false
            },
            selected: {
                type: Boolean,
                default: false
            },
            isFocused: {
                type: Boolean,
                default: false
            },
            color: {
                type: [String]
            },
            backgroundColor: {
                type: [String]
            },
            padding: {
                type: [String]
            },
            height: {
                type: [Number, String]
            },
        },
        data () {
            return {
                searchLabel: '',  // the slot value (textContent)
                autoComplete: false,
                state: 0,
                colors: [],
                backColors: [],
            };
        },
        computed: {
            classes () {
                return [
                    `${prefixCls}`,
                    !this.colorDefined && {
                        [`${prefixCls}-disabled`]: this.disabled,
                        [`${prefixCls}-selected`]: this.selected && !this.autoComplete,
                        [`${prefixCls}-focus`]: this.isFocused,
                    }
                ];
            },
            showLabel () {
                return (this.label) ? this.label : this.value;
            },
            optionLabel(){
                return this.label || (this.$el && this.$el.textContent);
            },
            styles() {
                const style = {};
                let { height } = this;
                if (height && typeof height !== 'string') {
                    height = `${height}px`;
                }
                this.showColor && (style.color = this.showColor);
                this.showBackColor && (style.backgroundColor = this.showBackColor);
                this.padding && (style.padding = this.padding);
                height && (style.height = height);
                return style;
            },
            eventProps() {
                var events = {};
                if (this.colorDefined){
                    events = {
                        mouseover: this.handleMouseOver,
                        mouseleave: this.handleMouseLeave,
                        mouseup: this.handleMouseUp,
                        mousedown: this.handleMouseDown,
                    };
                }

                return events;
            },
            showColor() {
                var result = null;
                if (this.colors && this.colors.length > 0) {
                    switch (this.state) {
                        case 1: result = ((this.colors.length > 1) && this.colors[1]) || this.colors[0]; break;
                        case 2: result = ((this.colors.length > 2) && this.colors[2]) || ((this.colors.length > 1) && this.colors[1]) || this.colors[0]; break;
                        default: result = this.colors[0]; break;
                    }
                }
                return result;
            },
            showBackColor() {
                var result = null;
                if (this.backColors && this.backColors.length > 0) {
                    switch (this.state) {
                        case 1: result = ((this.backColors.length > 1) && this.backColors[1]) || this.backColors[0]; break;
                        case 2: result = ((this.backColors.length > 2) && this.backColors[2]) || ((this.backColors.length > 1) && this.backColors[1]) || this.backColors[0]; break;
                        default: result = this.backColors[0]; break;
                    }
                }
                return result;
            },
            colorDefined() {
                return !!this.color || !!this.backgroundColor;
            }
        },
        methods: {
            start() {
                this.colors = this.applyColors(this.color);
                this.backColors = this.applyColors(this.backgroundColor);
                this.state = (this.selected && 1) || 0;
                this.$on('on-open-change', ()=>{
                    this.state = (this.selected && 1) || 0;
                });
            },
            changed() {
                const Select = findComponentUpward(this, 'uSelect');
                if (Select) this.autoComplete = Select.autoComplete;
            },
            select () {
                this.state = (this.selected && 1) || 0;

                if (this.disabled) return false;

                this.dispatch('uSelect', 'on-select-selected', {
                    value: this.value,
                    label: this.optionLabel,
                });
                this.$emit('on-select-selected', {
                    value: this.value,
                    label: this.optionLabel,
                });
            },
            handleMouseOver() {
                this.state = 1;
            },
            handleMouseLeave() {
                this.state = (this.selected && 1) || 0;
            },
            handleMouseDown() {
                this.state = 2;
            },
            handleMouseUp() {
                this.state = 1;
            },
            applyColors(color) {
                var result = [];
                var value = color || [];
                if (value instanceof Array) {
                    result = value;
                } else if (typeof value === 'string') {
                    result = value.split('|') || [];
                } else {
                    result = [value];
                }
                return result;
            }
        },
        mounted() {
            this.changed();
        },
        created() {
            this.start();
        },
        watch: {
            color() {
                this.colors = this.applyColors(this.color);
            },
            backgroundColor() {
                this.backColors = this.applyColors(this.backgroundColor);
            },
        }
    };
</script>



