import URL from 'url';

export const PI_2 = Math.PI * 2;
export const RAD_TO_DEG = 180 / Math.PI;
export const DEG_TO_RAD = Math.PI / 180;
export const URL_FILE_EXTENSION = /\.(\w{3,4})(?:$|\?|#)/i;
export const DATA_URI = /^\s*data:(?:([\w-]+)\/([\w+.-]+))?(?:;charset=([\w-]+))?(?:;(base64))?,(.*)/i;
export const SVG_SIZE = /<svg[^>]*(?:\s(width|height)=('|")(\d*(?:\.\d+)?)(?:px)?('|"))[^>]*(?:\s(width|height)=('|")(\d*(?:\.\d+)?)(?:px)?('|"))[^>]*>/i; // eslint-disable-line max-len
export const RETINA_PREFIX = /@([0-9\.]+)x/;

let nextUid = 0;

export function uid() {
    return ++nextUid;
}

export function hex2rgb(hex, out) {
    out = out || [];

    out[0] = ((hex >> 16) & 0xFF) / 255;
    out[1] = ((hex >> 8) & 0xFF) / 255;
    out[2] = (hex & 0xFF) / 255;

    return out;
}

export function hex2string(hex) {
    hex = hex.toString(16);
    hex = '000000'.substr(0, 6 - hex.length) + hex;

    return `#${hex}`;
}

export function rgb2hex(rgb) {
    return (((rgb[0] * 255) << 16) + ((rgb[1] * 255) << 8) + (rgb[2] * 255 | 0));
}

export function decomposeDataUri(dataUri) {
    const match = DATA_URI.exec(dataUri);
    const mediaType = (match && (match.length > 1) && match[1] && match[1].toLowerCase()) || null;
    const subType = (match && (match.length > 2) && match[2] && match[2].toLowerCase()) || null;
    const charset = (match && (match.length > 3) && match[3] && match[3].toLowerCase()) || null;
    const encoding = (match && (match.length > 4) && match[4] && match[4].toLowerCase()) || null;
    const data = (match && (match.length > 5) && match[5]) || null;
    return { mediaType, subType, charset, encoding, data };
}

export function getUrlFileExtension(url) {
    const match = URL_FILE_EXTENSION.exec(url);
    return (match && (match.length > 1) && match[1].toLowerCase()) ||  null;
}

export function getSvgSize(svgString) {
    const match = SVG_SIZE.exec(svgString);
    const size = {};
    if (match && (match.length > 7)) {
        size[match[1]] = Math.round(parseFloat(match[3]));
        size[match[5]] = Math.round(parseFloat(match[7]));
    }

    return size;
}

export function getResolutionOfUrl(url, defaultValue)
{
    const resolution = RETINA_PREFIX.exec(url);

    if (resolution)
    {
        return parseFloat(resolution[1]);
    }

    return defaultValue !== undefined ? defaultValue : 1;
}

export const TextureCache = Object.create(null);
export const BaseTextureCache = Object.create(null);

export function destroyTextureCache() {
    let key;
    for (key in TextureCache) {
        TextureCache[key].destroy();
    }
    for (key in BaseTextureCache) {
        BaseTextureCache[key].destroy();
    }
}

export function clearTextureCache() {
    let key;

    for (key in TextureCache) {
        delete TextureCache[key];
    }
    for (key in BaseTextureCache) {
        delete BaseTextureCache[key];
    }
}

let tempAnchor;

export function determineCrossOrigin(url, loc = window.location) {
    // data: and javascript: urls are considered same-origin
    if (url.indexOf('data:') === 0) {
        return '';
    }

    // default is window.location
    loc = loc || window.location;

    if (!tempAnchor) {
        tempAnchor = document.createElement('a');
    }

    // let the browser determine the full href for the url of this resource and then
    // parse with the node url lib, we can't use the properties of the anchor element
    // because they don't work in IE9 :(
    tempAnchor.href = url;
    url = URL.parse(tempAnchor.href);

    const samePort = (!url.port && loc.port === '') || (url.port === loc.port);

    // if cross origin
    if (url.hostname !== loc.hostname || !samePort || url.protocol !== loc.protocol) {
        return 'anonymous';
    }

    return '';
}



