<template>
    <canvas
        class="yui-svga"
        :id="innerId"
        ref="svga"
        @click="handleClicked"
        @mouseenter="handleMouseenter"
        @mouseover="handleMouseOver"
        @mouseleave="handleMouseLeave"
        @mousedown="handleMouseDown"
        @mouseup="handleMouseUp"
        :style="styles">
    </canvas>
</template>
<script>
    import mixinsSkin from '../../mixins/skin';
    import { uid } from '../../core';
    import { oneOf } from '../../utils/assist';
    import JSZip from 'jszip';
    import JSZipUtils from 'jszip-utils';
    import SVGA from 'svgaplayerweb';

    window.JSZip = JSZip;
    window.JSZipUtils = JSZipUtils;

    export default {
        name: 'svga',
        mixins: [ mixinsSkin ],
        props: {
            src: {
                type: [String],
                default: ''
            },
            autoPlay: {
                type: [Boolean],
                default: true
            },
            reverse: {
                type: [Boolean],
                default: false
            },
            paused: {
                type: [Boolean],
                default: false
            },
            loops: {
                type: [Number],
                default: 0
            },
            clipsToBounds: {
                type: [Boolean]
            },
            clearsAfterStop: {
                type: [Boolean],
                default: true
            },
            fillMode: {
                validator (value) {
                    return oneOf(value, ['Forward', 'Backward']);
                },
                default: 'Forward'
            },
            mode: {
                validator (value) {
                    return oneOf(value, ['ScaleToFill', 'AspectFill', 'AspectFit']);
                },
                default: 'ScaleToFill'
            },
            rang: {
                type: [String, Object, Array]
            },
            stepToFrame: {
                type: [String, Object, Array]
            },
            stepToPercentage: {
                type: [String, Object, Array]
            },
            setTextList: {
                type: [Array],
                default: () => []
            },
            setImageList: {
                type: [Array],
                default: () => []
            }
        },
        data() {
            return {
                innerW: 0,
                innerH: 0,
                innerId: `svga_${uid()}`,
                player: null,
                parser: null,
                videoItem: null,
                playing: false,
                loaded: false,
                stoped: true,
                rangeVal: null,
                textList: [],
                imageList: []
            };
        },
        computed: {
            styles() {
                const style = {};

                style.width = (this.innerW && `${this.innerW}px`) || 'auto';
                style.height = (this.innerH && `${this.innerH}px`) || 'auto';
                return style;
            },
        },
        methods: {
            start() {
                this.innerW = parseFloat(this.width || 0);
                this.innerH = parseFloat(this.height || 0);
                this.rangeVal = this.applyRang(this.rang);
                this.textList = this.setTextList || [];
                this.imageList = this.setImageList || [];
            },
            changed() {
                const url = this.src || this.skin || '';
                if (url !== this.url) {
                    this.stop();
                    this.url = url;
                    this.url && this.load();
                }
            },
            load() {
                if (!this.parser) {
                    this.parser = new SVGA.Parser(`#${this.innerId}`);
                }
                if (!this.player) {
                    this.player = new SVGA.Player(`#${this.innerId}`);
                    this.player.onFinished(()=> {
                        this.onFinished();
                    });
                    this.player.onFrame((frame)=> {
                        this.onFrame(frame);
                    });
                    this.player.onPercentage((percentage)=> {
                        this.onPercentage(percentage);
                    });
                }
                this.parser.load(this.url, (videoItem) => {
                    this.player.setVideoItem(videoItem);
                    this.loaded = !!videoItem;
                    this.onLoaded();

                    this.$nextTick(() => {
                        this.play();
                        this.paused && this.pause();
                    });
                }, ()=> {
                    this.onLoadError();
                });
            },
            play() {
                if (!this.player || !this.loaded || this.playing) {
                    return;
                }
                this.player.loops = this.loops || 0;
                this.player.clearsAfterStop = this.clearsAfterStop || true;
                this.player.fillMode = this.fillMode || 'Forward';
                if (this.textList.length > 0) {
                    this.textList.forEach(item => {
                        this.player.setText(item.value, item.forKey);
                    });
                }
                if (this.imageList.length > 0) {
                    this.imageList.forEach(item => {
                        this.player.setImage(item.imageUrl, item.forKey);
                    });
                }

                this.innerW && (this.$refs.svga.width = this.innerW);
                this.innerH && (this.$refs.svga.height = this.innerH);

                if (this.loaded) {
                    if (!this.rangeVal) {
                        this.player.startAnimation(this.reverse);
                    } else {
                        this.player.startAnimationWithRange(this.rangeVal, this.reverse);
                    }
                    this.playing = true;

                    this.$emit('on-playing');
                }
            },

            restart() {
                this.pause();
                this.play();
            },
            pause() {
                if (this.player && this.playing) {
                    this.player.pauseAnimation();

                    this.$emit('on-paused');
                }
                this.playing = false;
            },
            stop() {
                if (this.player && !this.stoped) {
                    this.player.stopAnimation();
                    this.$emit('on-stoped');
                }
                this.playing = false;
                this.stoped = true;
            },
            onFinished() {
                this.$emit('on-finished');
            },
            onFrame(frame) {
                this.$emit('on-frame', frame);
            },
            onPercentage(percentage) {
                this.$emit('on-percentage', percentage);
            },
            onLoaded() {
                this.$emit('on-loaded');
            },
            onLoadError() {
                this.$emit('on-loaded-error');
            },
            handleClicked (event) {
                this.$emit('on-click', event);
            },
            handleMouseenter(event) {
                this.$emit('on-mouseenter', event);
            },
            handleMouseOver(event) {
                this.$emit('on-mouseover', event);
            },
            handleMouseLeave(event) {
                this.$emit('on-mouseleave', event);
            },
            handleMouseDown(event) {
                this.$emit('on-mousedown', event);
            },
            handleMouseUp(event) {
                this.$emit('on-mouseup', event);
            },
            applyRang(val) {
                var result = null;
                var value = val || {};
                if (typeof value === 'string') {
                    value = value.split('||') || [];
                }
                if (value instanceof Array) {
                    if (value.length > 1 ) {
                        result.location = Number(value[0] || 0);
                        result.length = Number(value[0] || 0);
                    }
                } else {
                    const { location, length } = value;
                    result = { location, length };
                }
                if (result && (!result.location || !result.length)) {
                    result = null;
                }
                return result;
            },
            goToFrame(frame, andPlay) {
                this.player && this.loaded && this.player.stepToFrame(frame, andPlay && !this.paused);
            },
            goToPercentage(percentage, andPlay) {
                this.player && this.loaded && this.player.stepToPercentage(percentage, andPlay && !this.paused);
            },
        },
        watch: {
            width() {
                this.innerW = parseFloat(this.width || 0);
            },
            height() {
                this.innerH = parseFloat(this.height || 0);
            },
            src() {
                this.changed();
            },
            skin() {
                this.changed();
            },
            paused(val) {
                val ? this.pause() : this.play();
            },
            reverse() {
                this.restart();
            },
            clipsToBounds(val) {
                this.player && this.player.setClipsToBounds(val);
            },
            clearsAfterStop(val) {
                this.player && (this.player.clearsAfterStop = val);
            },
            fillMode(val) {
                this.player && (this.player.fillMode = val);
            },
            mode(val) {
                this.player && this.player.setContentMode(val);
            },
            rang(val) {
                this.rangeVal = this.applyRang(val);
                this.restart();
            },
            stepToFrame(val) {
                var frame = 0;
                var andPlay = false;
                var value = val || {};
                if (typeof value === 'string') {
                    value = value.split('||') || [];
                }
                if (value instanceof Array) {
                    (value.length > 0) && (frame = Number(value[0] || 0));
                    (value.length > 1) && (andPlay = (value[1] && (value[1] === 'true')));
                } else {
                    frame = value.frame || 0;
                    andPlay = value.andPlay || false;
                }
                this.goToFrame(frame, andPlay);
            },
            stepToPercentage(val) {
                var percentage = 0;
                var andPlay = false;
                var value = val || {};
                if (typeof value === 'string') {
                    value = value.split('||') || [];
                }
                if (value instanceof Array) {
                    (value.length > 0) && (percentage = Number(value[0] || 0));
                    (value.length > 1) && (andPlay = (value[1] && (value[1] === 'true')));
                } else {
                    percentage = value.percentage || 0;
                    andPlay = value.andPlay || false;
                }
                this.goToPercentage(percentage, andPlay);
            },
            setTextList(val) {
                this.textList = val || [];
                this.restart();
            },
            setImageList(val) {
                this.imageList = val || [];
                this.restart();
            }
        }
    };
</script>



