import Base from '../Base';

const MESSAGE = 'message';

export class TaskParam extends Base {
    constructor(uri, param) {
        super();
        this.uri = uri;
        this.param = param;
    }
}

export class BaseTask {
    constructor() {
        addEventListener(MESSAGE, this.onTask);
    }

    onTask() {
    }

    send(data) {
        postMessage(data);
    }

    destroy() {

    }
}



