export const SHAPES = {
    SHAPE: -1,
    POLY: 0,
    RECT: 1,
    CIRC: 2,
    ELIP: 3,
    RREC: 4,
};

export class Shape {
    constructor(shape = SHAPES.SHAPE, x = 0, y = 0) {
        this.x = Number(x);
        this.y = Number(y);
        this.type = shape;
    }

    get empty() {
        return true;
    }

    get bounds() {
        return null;
    }

    clone() {
        return Shape(this.x, this.y);
    }

    copy(info) {
        this.x = info.x;
        this.y = info.y;
        return this;
    }

    contains(x, y) {
        return (this.x === x) && (this.y === y);
    }

    equal(value) {
        return value && (value.type === this.type) && (value.x === this.x) && (value.y === this.y);
    }
}



