export default {
    props: {
        skin: {
            type: [Object, String, Array]
        },
        width: {
            type: [Number, String]
        },
        height: {
            type: [Number, String]
        },
        color: {
            type: [String, Array]
        },
        backgroundColor: {
            type: [String, Array]
        },
        mouseEnable: {
            type: [String]
        },
        textAlign: {
            type: [String]
        },
    },
    data () {
        return {
            url: '',
            timestamp: 0
        };
    },
    computed: {
        moused () {
            const { mouseEnable } = this;
            return mouseEnable && (mouseEnable.toLowerCase() === 'true');
        },
    },
    mounted() {
        this.changed();
    },
    created() {
        this.start();
    },
    beforeDestroy() {
        this.stop();
    },
    methods: {
        start() {

        },
        stop() {

        },
        changed() {
        }
    },
    watch: {
        skin() {
            this.changed();
        }
    }
};



