export default {
    name: 'ListExpand',
    functional: true,
    props: {
        item: [Object, String],
        render: Function,
        width: Number,
        height: Number,
        index: Number,
        selected: Boolean,
        hovered: Boolean,
    },
    render: (h, ctx) => {
        const params = {
            item: ctx.props.item,
            width: ctx.props.width,
            height: ctx.props.height,
            index: ctx.props.index,
            selected: ctx.props.selected,
            hovered: ctx.props.hovered,
        };
        return ctx.props.render(h, params);
    }
};



