<template>
    <component
        :class="['yui-select-dropdown', className, {'yui-select-dropdown-define': !!skin}]"
        :style="styles"
        :is="tagName"
        v-bind="tagProps">
        <slot></slot>
    </component>
</template>
<script>
    import Vue from 'vue';
    const isServer = Vue.prototype.$isServer;
    import { getStyle } from '../../utils/assist';
    const Popper = isServer ? function() {} : require('popper.js/dist/umd/popper.js');  // eslint-disable-line

    import { transferIndex, transferIncrease } from '../../utils/transfer-queue';

    import Clip from '../clip';
    import mixinsSkin from '../../mixins/skin';

    export default {
        name: 'Drop',
        mixins: [ mixinsSkin ],
        components: { Clip },
        props: {
            placement: {
                type: String,
                default: 'bottom-start'
            },
            className: {
                type: String
            },
            transfer: {
                type: Boolean
            },
            rule: {
                type: [Array, String, Object]
            },
        },
        data () {
            return {
                popper: null,
                minWidth: '',
                popperStatus: false,
                tIndex: this.handleGetIndex(),
                ruleInfo: {},
            };
        },
        computed: {
            tagName() {
                const {skin} = this;
                return (skin && Clip) || 'div';
            },
            tagProps() {
                const {skin} = this;
                const props = { };
                if (skin) {
                    props.mouseEnable = 'true';
                    this.url && (props.skin = this.url);
                    if (this.url && this.ruleInfo) {
                        this.ruleInfo.clips && (props.clips = this.ruleInfo.clips);
                        this.ruleInfo.grids && (props.grids = this.ruleInfo.grids);
                        this.ruleInfo.frame && (props.frame = parseInt(this.ruleInfo.frame));
                    }
                    // props.autosize = 'true';
                    this.width && (props.width = this.width);
                    this.height && (props.height = this.height);
                    this.color && (props.color = this.color);
                    props.slotClass = 'yui-select-dropdown-slot';
                    this.backgroundColor && (props.backgroundColor = this.backgroundColor);
                    this.textAlign && (props.textAlign = this.textAlign);
                }

                return props;
            },
            styles () {
                let style = {};
                // if (this.width) style.width = this.width;
                // if (this.height) style.height = this.height;
                // if (this.minWidth) style.minWidth = `${this.minWidth}px`;

                if (this.transfer) style['z-index'] = 1060 + this.tIndex;

                return style;
            }
        },
        methods: {
            start() {
                this.$on('on-update-popper', this.update);
                this.$on('on-destroy-popper', this.destroy);

                this.ruleInfo = this.reloadSource(this.rule);
            },
            changed() {
                this.url = ((this.skin !== 'none') && this.skin) || '';
            },
            update (parent) {
                if (isServer) return;
                parent = parent || this.$parent;

                if (this.popper) {
                    this.$nextTick(() => {
                        this.popper.update();
                        this.popperStatus = true;
                    });
                } else {
                    this.$nextTick(() => {
                        const refs = parent && parent.$refs;
                        const reference = refs && refs.reference;
                        reference && (this.popper = new Popper(reference, this.$el, {
                            placement: this.placement,
                            modifiers: {
                                computeStyle:{
                                    gpuAcceleration: false
                                },
                                preventOverflow :{
                                    boundariesElement: 'window'
                                }
                            },
                            onCreate:()=>{
                                this.resetTransformOrigin();
                                this.$nextTick(this.popper.update());
                            },
                            onUpdate:()=>{
                                this.resetTransformOrigin();
                            }
                        }));
                    });
                }
                // set a height for parent is Modal and Select's width is 100%
                if (parent.$options.name === 'uSelect') {
                    this.minWidth = parseInt(getStyle(parent.$el, 'width'));
                }
                this.tIndex = this.handleGetIndex();
            },
            destroy () {
                if (this.popper) {
                    setTimeout(() => {
                        if (this.popper && !this.popperStatus) {
                            this.popper.destroy();
                            this.popper = null;
                        }
                        this.popperStatus = false;
                    }, 300);
                }
            },
            resetTransformOrigin() {
                // 不判断，Select 会报错，不知道为什么
                if (!this.popper) return;

                let x_placement = this.popper.popper.getAttribute('x-placement');
                let placementStart = x_placement.split('-')[0];
                let placementEnd = x_placement.split('-')[1];
                const leftOrRight = x_placement === 'left' || x_placement === 'right';
                if(!leftOrRight){
                    this.popper.popper.style.transformOrigin = placementStart==='bottom' || ( placementStart !== 'top' && placementEnd === 'start') ? 'center top' : 'center bottom';
                }
            },
            handleGetIndex () {
                transferIncrease();
                return transferIndex;
            },
            reloadSource(rule) {
                var result = {};
                var value = rule || {};
                if (typeof value === 'string') {
                    value = value.split('||') || [];
                }
                if (value instanceof Array) {
                    (value.length > 0) && (result.clips = value[0]);
                    (value.length > 1) && (result.grids = value[1]);
                    (value.length > 2) && (result.frame = value[2]);
                } else {
                    const {clips, grids, frame} = value;
                    result = { clips, grids, frame };
                }
                return result;
            },
        },
        beforeDestroy () {
            if (this.popper) {
                this.popper.destroy();
            }
        },
        rule(value) {
            this.ruleInfo = this.reloadSource(value);
            this.changed();
        },
    };
</script>



