import Thread from './Thread';
import Dispatch from './Dispatch';
import Request from './Request';
import { ResTask, TaskParam, ResParam } from './tasks';
import { getUrlFileExtension, determineCrossOrigin } from '../utils';

const __EXTMAP = {
    'png': Request.IMAGE,
    'jpg': Request.IMAGE,
    'jpeg': Request.IMAGE,
    'svg': Request.SVG,
    'txt': Request.TEXT,
    'json': Request.JSON,
    'xml': Request.XML,
    'fnt': Request.FONT
};

var __baseTaskId = 0;

export default class Loader extends Dispatch {
    constructor() {
        super();
        this._worker = new Thread(ResTask.Scripts);
        let self = this;
        this._worker.on(Thread.ONMESSAGE, (data) => {
            self._onThreadMessage(data);
        });
    }

    init() {
        super.init();
        this._worker.init();
    }

    start() {
        super.start();
        this._worker.start();
    }

    stop() {
        this._worker.stop();
        super.stop();
    }

    destroy() {
        this._worker.destroy();
        super.destroy();
    }

    load(url, type, cache = false) {
        var result = null;
        var ext = type || getUrlFileExtension(url);
        type = (ext && __EXTMAP[ext]) || Request.DOCUMENT;
        switch (type) {
            case Loader.IMAGE:
                result = this._loadImage(url, cache);
                break;
            default:
                result = this._loadTask(url, type, cache);
                break;
        }
        return result;
    }

    _loadTask(url, type, cache) {
        var id = ++__baseTaskId;
        var param = new ResParam(id, url, type, null, cache);
        var task = new TaskParam(ResTask.ADD, param);
        this._worker && this._worker.send(task);

        return id;
    }

    _loadImage(url, cache) {
        const data = new Image();
        var id = ++__baseTaskId;
        var type = Request.IMAGE;
        var self = this;
        data.crossOrigin = determineCrossOrigin(url);
        data.onload = () => {
            self.emit(Loader.ONCOMPLETED, {id, url, data, type});
        };
        data.onerror = () => {
            self.emit(Loader.ONERROR, {url, id});
        };
        data.onloadprogress = (e) => {
            var loaded = e.loaded;
            var total = e.total;
            self.emit(Loader.ONERROR, {url, id, loaded, total});
        };
        data.src = url;
        cache && (Loader.__loadedCaches[url] = data);
        return id;
    }

    _onThreadMessage(data) {
        var code = data.code;
        var param = data.param;
        var id = param.id;
        var cache = param.cache;
        var url = param.url;
        if (code === ResTask.OK) {
            data = data.data;
            var type = data.type;
            data = data.data;
            cache && (Loader.__loadedCaches[url] = data);
            this.emit(Loader.ONCOMPLETED, {id, url, data, type});
        } else if (code === ResTask.ROGRESS) {
            data = data.data;
            var loaded = data.loaded;
            var total = data.total;
            this.emit(Loader.ONPROGRESS, {id, url, loaded, total});
        } else {
            this.emit(Loader.ONERROR, {url, id});
        }
    }

    static getRes(url) {
        return Loader.__loadedCaches[url];
    }

    static setRes(url, data) {
        Loader.__loadedCaches[url] = data;
    }

    static clearRes(url) {
        Loader.__loadedCaches[url] = null;
        delete Loader.__loadedCaches[url];
    }

    static clearAll() {
        Loader.__loadedCaches = {};
    }

    static get PNG() { return 'png'; }
    static get JPG() { return 'jpg'; }
    static get JPEG() { return 'jpeg'; }
    static get TXT() { return 'txt'; }
    static get JSON() { return 'json'; }
    static get FNT() { return 'fnt'; }

    static get ONCOMPLETED() { return 'Loader.ONCOMPLETED'; }
    static get ONPROGRESS() { return 'Loader.ONPROGRESS'; }
    static get ONERROR() { return 'Loader.ONERROR'; }
}

Loader.__loadedCaches = Object.create(null);



