import { App, Texture } from '../core';
export default {
    methods: {
        load(source, onUpdate) {
            var texture = source;
            if (!(source instanceof Texture)) {
                texture = App.assets().getTexture(source);
            }
            texture && texture.on(Texture.ONUPDATED, () => {
                onUpdate && onUpdate(texture);
            });
            texture && texture.valid && onUpdate && onUpdate(texture);
        },
        loadClips(source, clips, onUpdate) {
            this.load(source, (texture) => {
                if (!texture.valid) {
                    return;
                }
                const textures = (texture && texture.toClips.apply(texture, clips)) || [];
                (textures.length > 0) && onUpdate && onUpdate(textures);
            });
        },
        loadGrids(source, grids, width, height, onUpdate) {
            width = parseFloat(width);
            height = parseFloat(height);
            this.load(source, (texture) => {
                if (!texture.valid) {
                    return;
                }
                grids = grids || [];
                grids.length = 6;
                width && (grids[4] = width);
                height && (grids[5] = height);

                texture = (texture && texture.toGrid.apply(texture, grids));
                texture && onUpdate && onUpdate(texture);
            });
        },
        gridsVal(value) {
            var result = [];
            value = value || {};
            if (value instanceof Array) {
                result = value;
            } else if (typeof value === 'string') {
                result = value.split(',') || [];
            } else {
                const {left, top, right, bottom} = value;
                result = [left, top, right, bottom];
            }
            for (var i = 0; i < result.length; ++i) {
                result[i] = parseFloat(result[i]);
            }
            return result;
        },
        clipsVal(value) {
            var result = [];
            value = value || {};
            if (value instanceof Array) {
                result = value;
            } else if (typeof value === 'string') {
                result = value.split(',') || [];
            } else {
                const {clipX, clipY, splitX, splitY} = value;
                result = [clipX, clipY, splitX, splitY];
            }
            for (var i = 0; i < result.length; ++i) {
                result[i] = parseFloat(result[i] || 0);
            }
            return result;
        }
    }
};



